import React, { Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import ReactImageFallback from 'react-image-fallback';
import InfiniteScroll from 'react-infinite-scroller';
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST } from "./../../Constants/AppConstants";
import NoContent from '../Common/NoContent';

class list_view extends Component {
    constructor(props) {
        super(props)
        this.state = {
            loading: true,
            refreshing: false,
            showBottomNav: false,
            showHeaderNav: "backsort",

            hasMoreItems: true,
            pagenum: 1,

            total_page: 0,

            brandId: this.$f7route.params.brandId,
            featured: this.$f7route.params.featured ? this.$f7route.params.featured : false,
            brandName: '',

            products: [],

            errorMessage: '',

            sort: this.props.sort['sort'] ? this.props.sort['sort'] : 'asc',

            productLoading: false,

        }

        document.title = `Oil List - ${SITE_TITLE}`;
    }
    componentDidMount() {
        this.props.changeFooterNavbarShow(this.state.showBottomNav);
        this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
        this.loadMore();
        setTimeout(function(){ 
            this.pageTrack()
        }.bind(this), 3000);
    }

    pageTrack = () => {
        this.state.brandName ?
        AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
            module: "product",
            page_name: "Oil - "+ this.state.brandName,
            content_id: 0,
            category_id: this.state.brandId,
          }).then(results => {            
        })
        :
        ''
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.sort != prevProps.sort) {
            const self = this;
            self.$f7.preloader.show('red');
            this.setState({
                // products: [],
                hasMoreItems: true,
                pagenum: 1,
                total_page: 0,
                // productLoading: true,
            })
            this.loadMore(1);
        }
    }

    mergeArray = (arr1, arr2) => {
        const combined = Object.values(arr1.concat(arr2).reduce((r, o) => {
            r[o.id] = o;
            return r;
        }, {}));
        // return combined;
        return this.sortArrayOfObjects(combined, "price");
    }

    sortArrayOfObjects = (arr, key) => {
        return arr.sort((a, b) => {
            if (this.props.sort['sort'] == "asc") {
                return a[key] - b[key];
            }
            else {
                return b[key] - a[key];
            }
        });
    };


    loadMore = (page = null) => {
        AJAX_PUBLIC_REQUEST("POST", "product/getCategoryWiseProducts", {
            pagenum: page ? page : this.state.pagenum,
            category_id: 2,
            brand: this.state.brandId ? this.state.brandId : '',
            sort_by: "price",
            sort_value: this.props.sort['sort'],
            is_featured: this.state.featured ? this.state.featured  : ''
        }).then(results => {
            if (parseInt(results.response.code) === 1000) {
                this.setState({
                    brandName: results.response.brand_name
                })

                let products = [];


                if (results.response.data.length > 0) {
                    console.log(results.response)

                    if (this.state.hasMoreItems) {
                        products = this.mergeArray(this.state.products, results.response.data);

                        if (parseInt(results.response.pagenum) < results.response.total_page) {
                            this.setState({
                                products: products,
                                pagenum: parseInt(results.response.pagenum) + 1,
                                total_page: parseInt(results.response.total_page)
                            });
                        }
                        else if (parseInt(results.response.pagenum) == results.response.total_page) {
                            this.setState({
                                products: products,
                                hasMoreItems: false
                            });
                        }
                        else {
                            this.setState({
                                hasMoreItems: false
                            });
                        }
                    }

                } else {
                    this.setState({
                        loading: false,
                        productLoading: false,
                        hasMoreItems: false,
                        products: products,
                    })
                }

            } else {
                this.setState({
                    hasMoreItems: false,
                    pagenum: 1,
                    total_page: 0
                })
            }

            if (this.state.loading) {
                this.setState({
                    loading: false,
                });
            }
            if (this.state.productLoading) {
                this.setState({
                    productLoading: false,
                });
            }
            const self = this;
            self.$f7.preloader.hide();
        });
    }

    reduceStr = (str, size) => {
        if (str.length > 0) {
            if (str.length > size) {
                str = str.substring(0, size) + '..';
            }
        }
        else {
            str = '---';
        }
        return str;
    }

    onPageAfterIn = () => {
        setTimeout(function(){
          console.log("Action")
          this.props.changeFooterNavbarShow(this.state.showBottomNav);
          this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
        }.bind(this), 10);
    }


    onPtrRefresh = (done) => {
        setTimeout(() => {
          this.loadMore();
          done();
        }, 1000);
    }




    render() {


        return (
            this.state.loading ?
                <Frc.Page onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
                    <RandomLoader />
                </Frc.Page>
                :
                <Frc.Page className="categories-list-page" style={{ height: '100vh' }}  onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}
                    infinite
                    infiniteDistance={50}
                    infinitePreloader={this.state.hasMoreItems}
                    onInfinite={() => this.loadMore()}
                >
                    <Frc.BlockTitle className="categories-title">{this.state.brandName ? this.state.brandName : "Oil"}</Frc.BlockTitle>
                    <Frc.List className="categories-list"
                    >
                        {
                            this.state.productLoading ?
                            <div style={{width: '100%', textAlign: 'center'}}>
                                <i className="fas fa-spinner fa-spin fa-2x fa-fw"></i>
                            </div>
                            :
                            this.state.products.length > 0 ?
                                
                                            this.state.products.map((product, index) =>
                                                <div key={index} style={{ padding: '0 10px' }}>
                                                    <Frc.Link className="no-ripple" href={`/OilDetails/product/` + product['id'] + `/`} noFastClick>
                                                        <Frc.Row>
                                                            <Frc.Col width="40">
                                                                <div className="categories-thumb ripple">
                                                                    <ReactImageFallback
                                                                        src={product['photo']}
                                                                        fallbackImage='./../static/placeholder_dark.svg'
                                                                        initialImage='./../static/placeholder_dark.svg'
                                                                        alt='image'
                                                                        className="swiper-lazy"
                                                                    />
                                                                </div>
                                                            </Frc.Col>
                                                            <Frc.Col width="60">
                                                                <div className="categories-details">
                                                                    <div className="name">{this.reduceStr(product['title'], 10)}</div>
                                                                    <div className="capacity">Engine Grade : {product['engine_grade'] ? product['engine_grade'] : '---'}</div>
                                                                    <div className="product-price">
                                                                        { 
                                                                            (product['discount'] && product['discount'] > 0) ?
                                                                            <span className="present-price">Tk. {product['discount']}</span>
                                                                            :
                                                                            <span className="present-price">Tk. {product['price']}</span>
                                                                        }
                                                                        {product['discount'] > 0 ? <span className="old-price">Tk. {product['price']}</span> : ''}
                                                                        <i className="icon icon-next ripple"></i>
                                                                    </div>
                                                                    <div className="book-now">
                                                                        Order Now
                                                                    </div>
                                                                </div>
                                                            </Frc.Col>
                                                        </Frc.Row>
                                                    </Frc.Link>
                                                </div>
                                            )
                                :
                                <div className="notification-details" style={{width: '100%'}}>
                                    <NoContent icon = 'fas fa-oil-can' text="No Oil Found!" />
                                </div>
                        }
                    </Frc.List>
                </Frc.Page>
        )
    }
}


function mapStateToProps(state) {
    return {
        sort: state.sort
    }
}


const mapDispachToProps = (dispach) => {
    return {
        changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
        changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
    }
}

export default connect(mapStateToProps, mapDispachToProps)(list_view);
