import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, AJAX_REQUEST, GET_STORAGE, SET_STORAGE, ADD_TO_CART, USER, ITEM_COUNT, REMOVE_STORAGE } from "../../Constants/AppConstants";
import { RandomLoader } from '../Common/ReactContentLoader';
import Modal from 'react-responsive-modal';


class CheckoutGuest extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "onlyback",

      errorMessage: '',
      item_on_cart: 1,



      customername: '',
      mobileNumber: '',
      emailAddress: '',

      deliveryOption: '',

      address: '',
      city: '',
      postalCode: '',

      shippingType: '',
      paymentType: '',

      paymentOptions: [],
      shippingOptions: [],
      speedonxCollect: [],

      subTotal: 0,
      discountTotal: 0,
      deliveryCharge: 0,
      total: 0,

      open: false,
      status: '',
      message: '',

    }
  }

  componentDidMount() {
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
      this.setState({
        loading: false
      })
      this.handel_cart_state();
      this.getPaymentOptions();
      this.getShippingOptions();
      this.getdeliveryCollectFromSpeedonX();
  }

  showToastIcon(text) {
    const self = this;
    // Create toast
    if (!self.toastIcon) {
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 3000,
      });
      self.toastIcon.open();
    }
    else {
      self.toastIcon.close();
      self.toastIcon.destroy();
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 3000,
      });
      self.toastIcon.open();
    }
  }

  handleSubmit = () => {
    let products = [];
    if (ITEM_COUNT() > 0) {
      const cart = JSON.parse(GET_STORAGE("cart"));
      cart.forEach(function (item, key) {
        let pr = {};
        {
          pr['product_id'] = item.id;
          pr['quantity'] = item.quantity;
          pr['price'] = parseInt(item.discounted_price) > 0 ? parseInt(item.discounted_price) : item.price;
        }
        products.push(pr)
      }.bind(this));
      
    }
    if(this.state.customername == ''){
      this.showToastIcon('Please Enter Your Name')
      return;
    }
    else if(this.state.mobileNumber == ''){
      this.showToastIcon('Please Provide Your Mobile Number')
      return;
    }
    else if(this.state.deliveryOption == ''){
      this.showToastIcon('Please Select a Delivery Option')
      return;
    }
    else if((this.state.address == '' || this.state.city == '' || this.state.postalCodeNew == '') && this.state.deliveryOption == 2){
      this.showToastIcon('Address is not Complete')
      return;
    }
    else if(this.state.shippingType == '' && this.state.deliveryOption == 2){
      this.showToastIcon('Please Select a Shipping Type')
      return;
    }
    else if(this.state.paymentType == ''){
      this.showToastIcon('Please Select a Payment Option')
      return;
    }
   
    AJAX_PUBLIC_REQUEST('POST','user/saveOrder',{
      customer_id: 0,
      customername: this.state.customername,
      mobileNumber: this.state.mobileNumber,
      emailAddress: this.state.emailAddress,
      deliveryOption: this.state.deliveryOption,
      deliveryCharge: this.state.deliveryCharge,
      address: this.state.address ? this.state.address : '',
      city: this.state.city ? this.state.city : '',
      zipCode: this.state.postalCode ? this.state.postalCode : '',
      shippingOption: this.state.shippingType,
      paymentOption: this.state.paymentType,
      // totalAmount: this.state.total+ this.state.deliveryCharge ? parseInt(this.state.deliveryCharge) : 0,
      totalAmount: this.state.total + parseInt(this.state.deliveryCharge),
      products: products,
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            address: '',
            city: '',
            postalCode: '',

            deliveryOption: '',

            shippingType: '',
            paymentType: '',
            subTotal: 0,
            discountTotal: 0,
            deliveryCharge: 0,
            total: 0,
            open: true,
            status: results.response.status,
            message: results.response.message,
          })
          REMOVE_STORAGE('cart');
        }
        else {
          this.showToastIcon('Something went Wrong!')
        }
      });
  } 

  onOpenModal = () => {
    this.setState({ open: true });
  };
 
  onCloseModal = () => {
    this.setState({ open: false });
  };

  getPaymentOptions = () => {
    AJAX_PUBLIC_REQUEST('POST','user/getPaymentOptions',{
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            paymentOptions: results.response.data,
          })
        } else {
          this.setState({
            paymentOptions: [],
          })
        }
      });
  } 

  getShippingOptions = () => {
    AJAX_PUBLIC_REQUEST('POST','user/getShippingOptions',{
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            shippingOptions: results.response.data,
          })
        } else {
          this.setState({
            shippingOptions: [],
          })
        }
      });
  }

  getdeliveryCollectFromSpeedonX = () => {
    AJAX_PUBLIC_REQUEST('POST','shop/deliveryCollectFromSpeedonX',{
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            speedonxCollect: results.response.data,
          })
        } else {
          this.setState({
            speedonxCollect: [],
          })
        }
      });
  } 

  handleChange = event => {
    const { name, value } = event.target
    if(name == 'shippingType'){
      this.setState({
        deliveryCharge: value
      })
    }
    else if(name == 'deliveryOption'){
      if(value == 1){
        this.setState({
          shippingType: '',
          deliveryCharge: 0
        })
      }
    }
    this.setState({
      [name]: value
    },()=> {
      // console.log(name, value)
    })
  }

  //== calculation of cart summery ==//
  cal_cart_summery = () => {
    let subTotal = 0;
    let discountTotal = 0;
    let price = 0;
    if (ITEM_COUNT() > 0) {
      const cart = JSON.parse(GET_STORAGE("cart"));
      const redeem_point_status = false;
      // const redeem_point_status = GET_STORAGE("redeem_point_status") && (GET_STORAGE("redeem_point_status") == 'yes') ? true : false;
      cart.forEach(function (item, key) {
        price = parseInt(item.discounted_price) > 0 ? parseInt(item.discounted_price) : parseFloat(item.price);
        subTotal = subTotal + (parseInt(item.quantity) * price);
        item.discounted_price != '' ? discountTotal = discountTotal + (parseInt(item.quantity) * parseFloat(item.discounted_price)) : '';
      }.bind(this));

      this.setState({
        subTotal: subTotal,
        discountTotal: discountTotal,
        // total: discountTotal > 0 ? subTotal - discountTotal : subTotal
        total: subTotal
      });

    }
    else {
      this.setState({
        subTotal: 0,
        discountTotal: 0,
        total: 0
      });
    }
  }

  //== handle cart state ==//
  handel_cart_state = () => {
    // {
    //   this.props.isAuthenticated ?
        this.setState({
          products: JSON.parse(GET_STORAGE("cart")) ? JSON.parse(GET_STORAGE("cart")) : []
        })
    //     :
    //     ""
    // }
    this.cal_cart_summery();
  }

  handleCalculation = (event, subvalue, deliveryCharge) => {
    subvalue = subvalue ? subvalue : 0;
    const { name, value } = event.target;
    // console.log(subvalue, value)
      this.setState({
        deliveryCharge: parseInt(deliveryCharge)
      })
    this.setState({
      [name]: value
    },()=> {
      let subTotal = this.state.subTotal;      
      parseInt(subvalue) <= parseInt(subTotal) && parseInt(subvalue) != '0' ?
        this.setState({
          deliveryCharge: 0
        })
      : ''

    })
  }
  

  render() {
    const { open } = this.state;
    return (
      this.state.loading ?
      <Frc.Page>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Frc.Page name="" className="p-b-45vw myCheckoutGuestPage" noSwipeback>
        <Fragment>
          {/* Modal */}
        <Modal closeOnOverlayClick={false} modalId="signupPopup" closeIconId="closeSignupPop" open={open} onClose={this.onCloseModal} center>
          {
            this.state.status == "success" ?
            <Frc.Link className="successSignup" iconIos="f7:checkmark_alt_circle" iconMd="material:check_circle_outline" href="#" />
            :
            <Frc.Link className="errorSignup" iconIos="f7:info_circle_fill" iconMd="material:error" href="#" />
          }
          
          <h2>{this.state.message}</h2>
          <Frc.Link className="fw-login" href="/" onClick={()=>this.onCloseModal()} animate={false} reloadCurrent={true}>Ok</Frc.Link>
        </Modal>
          <div className="checkout-guest-wrapper">

            <div className="c-g-info checkout-guest-info">
              <h3>Place your order in less than a minute.</h3>
              <div className="checkout-guest-form">
              <Frc.List form>
                  <Frc.ListInput
                    type="text"
                    placeholder="Name"
                    value={this.state.customername}
                    onChange={this.handleChange} 
                    name="customername"
                    
                  />
                  <Frc.ListInput
                    type="text"
                    placeholder="Mobile Number"
                    value={this.state.mobileNumber}
                    onChange={this.handleChange} 
                    name="mobileNumber"
                    
                  />
                  <Frc.ListInput
                    type="text"
                    placeholder="Email"
                    value={this.state.emailAddress}
                    name="emailAddress"
                    onChange={this.handleChange}
                  />
                </Frc.List>
              </div>
            </div>

            <div className="c-g-info delivery-opt-info">
              <h3>Delivery Options</h3>
              <div className="checkout-guest-form checkout-guest-form-opt">
                <div className="checkout-opt-list">
                  <div className="checkout-opt-radio">
                    <Frc.Radio name="deliveryOption" value="1" onChange={this.handleChange} />
                  </div>
                  <div className="checkout-opt-icon">
                    <img src='../static/icons/bag.png' />
                  </div>
                  <div className="checkout-opt-address">
                    <h4>Collect from SpeedonX <span>You must collect your product within 24 hours</span></h4>
                    <div className="checkout-opt-address-content" style={this.state.deliveryOption == 1 ? {height : '100%', visibility: 'visible', opacity: '1', transition: '.15s'} : {height: 0, visibility: 'hidden', opacity: '0', transition: '.15s'} } >
                      <p>{this.state.speedonxCollect['para_1'] ? this.state.speedonxCollect['para_1'] : ''}</p>
                      <span style={{color: '#ef2741'}}>{this.state.speedonxCollect['address'] ? this.state.speedonxCollect['address'] : ''}</span>
                      <p>{this.state.speedonxCollect['para_2'] ? this.state.speedonxCollect['para_2'] : ''}</p>
                    </div>
                  </div>
                </div>
                <div className="checkout-opt-list">
                  <div className="checkout-opt-radio">
                    <Frc.Radio name="deliveryOption" value="2" onChange={this.handleChange}/>
                  </div>
                  <div className="checkout-opt-icon">
                    <img src='../static/icons/car.png' />
                  </div>
                  <div className="checkout-opt-address">
                    <h4> Home Delivery <span>SpeedonX will ship your product at your delivery address.</span></h4>
                  </div>

                </div>
              </div>
              {/* {
                this.state.deliveryOption == 2 ? */}
                <div className="checkout-opt-address-change" style={this.state.deliveryOption == 2 ? {height : '100%', visibility: 'visible', opacity: '1', transition: '.15s'} : {height: 0, visibility: 'hidden', opacity: '0', transition: '.15s'} }>
                <h4>Billing/Shipping Address</h4>
                <div className="checkout-guest-form">
                  <Frc.List form>
                      <Frc.ListInput
                        type="text"
                        placeholder="Address"
                        name="address"
                        value={this.state.address}
                        onChange={this.handleChange}
                      />
                      <Frc.ListInput
                        type="text"
                        placeholder="City"
                        name="city"
                        value={this.state.city}
                        onChange={this.handleChange}
                      />
                      <Frc.ListInput
                        type="text"
                        placeholder="Zip/Post Code"
                        name="postalCode"
                        value={this.state.postalCode}
                        onChange={this.handleChange}
                      />
                  </Frc.List>
                  <span className="form-note">
                    Delivery charges might vary on product weight and delivery location. And you will be contacted by SpeedonX.
                  </span>
                </div>
              </div>
                {/* :
                ''
              } */}
            </div>

            <div className="c-g-info shipping-opt-info" style={this.state.deliveryOption == 2 ? {height : '100%', visibility: 'visible', opacity: '1', transition: '.15s'} : {height: '0px', visibility: 'hidden', opacity: '0', transition: '.15s', padding: 0, margin: '-7px'} }>
              <h3>Shipping Options</h3>
              <div className="checkout-guest-form checkout-guest-form-opt">
              {
                  this.state.shippingOptions ?
                    this.state.shippingOptions.length > 0 ?
                      this.state.shippingOptions.map((shippingOption, index) => 
                        <div key={`shipping`+index} className="checkout-opt-list">
                          <div className="checkout-opt-radio">
                            <Frc.Radio name="demo-radio-inline" name="shippingType" onChange={(e)=>this.handleCalculation(e, shippingOption['sub_value'], shippingOption['value'])}  value={shippingOption['sub_value_key'] > 0 ? shippingOption['sub_value_key'] : shippingOption['value_key']} />
                          </div>
                          <div className="checkout-opt-address">
                            <h4>{shippingOption['name']} - <span className="price">৳ {shippingOption['value']}</span>
                            {
                              shippingOption['details'] ?
                              <span><span className="must">*</span> {shippingOption['details']}</span>
                              : ''
                            }
                            {
                              shippingOption['sub_name'] || shippingOption['sub_value'] ?
                              <span><span className="must">*</span> {shippingOption['sub_name']} {shippingOption['sub_value']}</span>
                              : ''
                            }
                            </h4>
                          </div>
                        </div>
                      
                      )
                      :
                      <div className="checkout-opt-list">
                        <div className="checkout-opt-address">
                          <h4>No Shipping Options Available!</h4>
                        </div>
                      </div>
                    :
                    <div className="checkout-opt-list">
                      <div className="checkout-opt-address">
                        <h4>No Shipping Options Available!</h4>
                      </div>
                    </div>
              }
              </div>
            </div>

            <div className="c-g-info delivery-opt-info">
              <h3>Payment Options</h3>
              <div className="checkout-guest-form checkout-guest-form-opt">
              {
                  this.state.paymentOptions ?
                    this.state.paymentOptions.length > 0 ?
                      this.state.paymentOptions.map((paymentOption, index) => 
                      <div key={`pay`+index} className="checkout-opt-list">
                        <div className="checkout-opt-radio">
                          <Frc.Radio name="demo-radio-inline" name="paymentType" onChange={this.handleChange} value={paymentOption['value']} />
                        </div>
                        <div className="checkout-opt-icon">
                          <img src={paymentOption['image']} />
                        </div>
                        <div className="checkout-opt-address">
                          <h4> {paymentOption['name']} <span>{paymentOption['details']}</span></h4>
                        </div>
                      </div>
                      
                      )
                      :
                      <div className="checkout-opt-list">
                        <div className="checkout-opt-address">
                          <h4>No Payment Options Available!</h4>
                        </div>
                      </div>
                    :
                    <div className="checkout-opt-list">
                      <div className="checkout-opt-address">
                        <h4>No Payment Options Available!</h4>
                      </div>
                    </div>
              }
              </div>
            </div>

            <div className="c-g-info cart-payable-ammount">
              <h3>Payable Amount</h3>
              <div className="cart-total-wrapper">
                  <div className="cart-total">
                    <span>Subtotal : ৳ <span className="price">{this.state.subTotal}</span></span>
                    <span className="discount">Delivery Charge : ৳ <span className="price">{this.state.deliveryCharge ? parseInt(this.state.deliveryCharge) : 0}</span></span>
                    {/* <span className="discount">Discount on Products : ৳ <span className="price">{this.state.discountTotal}</span></span> */}
                  </div>
                  <div className="cart-total-price">
                    Total To Pay : ৳ <span className="price">{this.state.total + parseInt(this.state.deliveryCharge)}</span>
                  </div>
                </div>
            </div>

            <div className="c-g-info cart-payable-btn">
              <Frc.Link className="cart-payable-Cusbtn" onClick={this.handleSubmit}>Complete Order</Frc.Link>
            </div>

          </div>
        </Fragment>
      </Frc.Page >
    );
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(null, mapDispachToProps)(CheckoutGuest);