import React, { Component, Fragment } from 'react';
import {
  Panel,
  Link,
  Block,
  NavLeft, 
  Icon, 
  Page, 
  Navbar
} from 'framework7-react';


import '../../css/fontawesome-pro.css';

import Logout from './../../pages/Auth/Logout';

import { connect } from 'react-redux';

import './../../css/PopUpCss.css';

import ReactImageFallback from 'react-image-fallback';

import { AJAX_REQUEST, AJAX_PUBLIC_REQUEST, GET_STORAGE, USER } from "./../../Constants/AppConstants";
let user_info = [];

class LeftPanel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user_info: [],
      name: '',
      image: '',
      links: [],
      showSupport: "no",
    }
  }
  componentDidMount() {
    this.getSocialLinks();
    if(this.props.isAuthenticated){
      user_info = GET_STORAGE(USER);
      if (user_info) {
        this.getCustomerProfile();
        this.getCustomerProfileInterval();
        // this.getShowSupport();
      }
    }   
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.isAuthenticated != prevProps.isAuthenticated) {
        user_info = GET_STORAGE(USER);
        if (user_info) {
          this.getCustomerProfile();
          this.getCustomerProfileInterval();
        }
    }
  }

  getShowSupport = () => {
    AJAX_REQUEST('POST','setting/showSupport',{
      customer_id: JSON.parse(user_info)['id']
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            showSupport: results.response.data
          })
        }
      });
  }

  getCustomerProfile = () => {
    if (user_info) {
      AJAX_REQUEST('POST','user/getCustomerProfile',{
        customer_id: JSON.parse(user_info)['id']
      }).then(results => {
          if (parseInt(results.response.code) === 1000) {
            this.setState({
              user_info: results.response.data,
              name: results.response.data.name,
            })
          }
        });
    }
  }

  getCustomerProfileInterval = () => {
    setInterval(function(){ 
      if (user_info) {
        AJAX_REQUEST('POST','user/getCustomerProfile',{
          customer_id: JSON.parse(user_info)['id']
        }).then(results => {
            if (parseInt(results.response.code) === 1000) {
              this.setState({
                user_info: results.response.data,
                name: results.response.data.name,
              })
            }
          });
      }
     }.bind(this), 3000);
  }

  getSocialLinks = () => {
    AJAX_PUBLIC_REQUEST('POST','home/socialLinks',{
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            links: results.response.data
          })
        }
      });
  }


  render() {
    return (
      <Fragment>
          {/* Left panel with cover effect*/}

          <Panel className="main-panel" left cover themeDark swipeActiveArea={10} swipe={true}>
            {/* <View> */}
            <Page className="nav-menu-page">
              <Navbar className="nav-menu-header">
                <NavLeft>
                  <Link panelClose>
                    <Icon ios="f7:chevron_left" aurora="f7:arrow_back" md="material:arrow_back"></Icon>
                  </Link>
                </NavLeft>
              </Navbar>
              <Block className="no-margin no-padding">
              {
                this.props.isAuthenticated ?
                <div className="nav-menu-container nav-user-info">
                  <Link className="nav-about ripple" href="/user-profile/" panelClose view=".view-main">
                    <div className="nav-user-info-img">
                      <ReactImageFallback
                          src={this.state.user_info['photo']}
                          fallbackImage='./../static/placeholder_light.png'
                          initialImage='./../static/placeholder_light.png'
                          alt='image'
                          className=""
                        />
                    </div>
                    <div className="nav-user-info-name">
                      {this.state.name} <span>User Account</span>
                    </div>

                  </Link>
                </div>
                :
                // <div className="nav-menu-container">
                //   <p className="header-gap"></p>
                // </div>
                <div className="nav-menu-container nav-user-info">
                  <Link className="nav-about no-ripple" href="#">
                    <div className="nav-user-info-img">
                      <ReactImageFallback
                          src='./../static/placeholder_light.png'
                          fallbackImage='./../static/placeholder_light.png'
                          initialImage='./../static/placeholder_light.png'
                          alt='image'
                          className=""
                        />
                    </div>
                    <div className="nav-user-info-name">
                      Welcome<span>Guest User</span>
                    </div>

                  </Link>
                </div>
              }

                <div className="nav-menu-container">
                  <ul className="nav-menu-list">
                    <li>
                      <Link className="nav-about" href="/about/" panelClose view=".view-main">About Us</Link>
                    </li>
                    <li>
                      <Link className="nav-video" href={this.state.links ? this.state.links['youtube'] : ''} external panelClose view=".view-main">SpeedonX Video Chanel</Link>
                    </li>
                    <li>
                      <Link className="nav-tips" href="/TipsnTricks/" panelClose view=".view-main">Tips &amp; Tricks</Link>
                    </li>
                    {
                        this.props.isAuthenticated ?
                        <li>
                          <Link className="nav-orders" href="/MyOrder/" panelClose view=".view-main">My Orders</Link>
                        </li>
                        :
                        ''
                    }
                    {/* <li>
                      <Link className="nav-orders" href="/Cart/" panelClose view=".view-main">Cart</Link>
                    </li> */}
                    {/* {
                        this.props.isAuthenticated ?
                        <li>
                          <Link className="nav-orders" href="/Cart/" panelClose ignoreCache={true} view=".view-main">Cart</Link>
                        </li>
                        :
                        ''
                    } */}
                    <li>
                      <Link className="nav-loyalty" href="/CategoriOffer/" panelClose view=".view-main">Loyalty Offers</Link>
                    </li>
                    {/* <li>
                      <Link className="nav-insurance" href="/oilhome/" panelClose ignoreCache={true} view=".view-main">Online Insurance</Link>
                    </li> */}
                    {/* {
                        this.state.showSupport == "yes" ?
                        <li>
                          <Link className="nav-orders" href="#" 
                          onClick={() => {
                                if (typeof AndroidInterface != "undefined") {
                                    AndroidInterface.showSupport();
                                } else {
                                    window.webkit.messageHandlers.showSupportTickets.postMessage("Hello from JavaScript iOS Call: ");
                                }
                            }} 
                          panelClose view=".view-main">Support</Link>
                        </li>
                        :
                        ''
                    } */}
                    
                    {
                      this.props.isAuthenticated ?
                      <li>
                      <Link className="nav-support" href="#" 
                        onClick={() => {
                          if (typeof AndroidInterface != "undefined") {
                              AndroidInterface.showSupport();
                          } else {
                              window.webkit.messageHandlers.showSupportTickets.postMessage("Hello from JavaScript iOS Call: ");
                          }
                        }} 
                        panelClose view=".view-main">Support</Link>
                        </li>
                        :
                        ''
                    }
                  
                    <li>
                      {
                        this.props.isAuthenticated ?
                          <Logout />
                          :
                          <Link className="nav-logout" href="/login/" panelClose view=".view-main">Login</Link>
                      }
                    </li>
                  </ul>
                </div>
                <div className="nav-menu-container legal-menu">
                  <h3 className="title">Legal</h3>
                  <ul className="nav-menu-list">
                    <li>
                      <Link className="" href="/PrivacyPolicy/" panelClose view=".view-main">Privacy Policy</Link>
                    </li>
                    <li>
                      <Link className="" href="/Terms/" panelClose view=".view-main">Terms &amp; Conditions</Link>
                    </li>
                  </ul>
                </div>
                <div className="nav-menu-container nav-social-menu">
                  <h3 className="title">Social Profiles</h3>
                  <ul className="nav-menu-list">
                    {
                      this.state.links ?
                      <Fragment>
                        {
                          this.state.links['facebook'] ?
                          <li>
                            <Link className="" external href={this.state.links ? this.state.links['facebook'] : ''}>
                              <i className="fab fa-facebook-f"></i>
                            </Link>
                          </li>
                          :
                          ''
                        }
                        {
                          this.state.links['twitter'] ?
                          <li>
                            <Link className="" external href={this.state.links ? this.state.links['twitter'] : ''}>
                              <i className="fab fa-twitter"></i>
                            </Link>
                          </li>
                          :
                          ''
                        }
                        {
                          this.state.links['instagram'] ?
                          <li>
                            <Link className="" external href={this.state.links ? this.state.links['instagram'] : ''}>
                              <i className="fab fa-instagram"></i>
                            </Link>
                          </li>
                          :
                          ''
                        }
                        {
                          this.state.links['youtube'] ?
                          <li>
                            <Link className="" external href={this.state.links ? this.state.links['youtube'] : ''}>
                              <i className="fab fa-youtube"></i>
                            </Link>
                          </li>
                          :
                          ''
                        }
                        {
                          this.state.links['linkedin'] ?
                          <li>
                            <Link className="" external href={this.state.links ? this.state.links['linkedin'] : ''}>
                              <i className="fab fa-linkedin-in"></i>
                            </Link>
                          </li>
                          :
                          ''
                        }
                      </Fragment>
                      :
                      ''
                    }
                  </ul>
                </div>
              </Block>
            </Page>
            {/* </View> */}
          </Panel>
      </Fragment>
    )
  }
}
function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    auth: state.auth,
    setting: state.setting
  }
}

export default connect(mapStateToProps, null)(LeftPanel);