import React, { Fragment, Component } from 'react';
import { connect } from 'react-redux';
import { AJAX_PUBLIC_REQUEST } from '../../Constants/AppConstants';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import {
    Page,
    Link,
    List,
    ListInput,
    Button, 
    Navbar,
    NavLeft
} from 'framework7-react';
import Modal from 'react-responsive-modal';

class ForgetPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            vcode: '',
            loading: true,
            open: false,

            showBottomNav: false,
            showHeaderNav: "hide",

            pleaseWait: false,

            customerId: '',

            status: '',
            message: '',

        }
    }

    componentDidMount() {
        this.props.changeFooterNavbarShow(this.state.showBottomNav);
        this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
        this.setState({ loading: false })
        // AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
        //     page_name: "login",
        //     content_id: 0,
        //     module: "user",
        // }).then(results => {            
        // });
    }

    changeHandler = (e) => {
        const { name, value } = event.target
        this.setState({
        [name]: value
        },()=> {
        // console.log(this.state);
        })
    }


    onSubmit = (e) => {
        e.preventDefault();
        this.setState({
            pleaseWait: true,
        });
        AJAX_PUBLIC_REQUEST("POST", "user/checkResetCode", {
            code: this.state.vcode
        }).then(results => {
            if (parseInt(results.response.code) === 1000) {
                this.setState({
                    open: true,
                    status: results.response.status,
                    message: results.response.message,
                    pleaseWait: false,
                    customerId: results.response.customer_id,
                })
            } else {
                this.setState({
                    pleaseWait: false,
                })
              this.showToastIcon(results.response.message)
            }
          });

    }

    onOpenModal = () => {
        this.setState({ open: true });
      };
     
      onCloseModal = () => {
        this.setState({ open: false });
      };


    showToastIcon(text, type) {
        const self = this;
        let status_ios = (type == "Success") ? '<i class="f7-icons">checkmark_circle_fill</i>' : '<i class="f7-icons">info_circle_fill</i>';
        let status_andr = (type == "Success") ? '<i class="material-icons">check_circle</i>' : '<i class="material-icons">info</i>';
        // Create toast
        if (!self.toastIcon) {
          self.toastIcon = self.$f7.toast.create({
            icon: self.$theme.ios || self.$theme.aurora ? status_ios : status_andr,
            text: text,
            position: 'center',
            closeTimeout: 2000,
          });
          self.toastIcon.open();
        }
        else {
            self.toastIcon.close();
            self.toastIcon.destroy();
            self.toastIcon = self.$f7.toast.create({
                icon: self.$theme.ios || self.$theme.aurora ? status_ios : status_andr,
                text: text,
                position: 'center',
                closeTimeout: 2000,
            });
            self.toastIcon.open();
          }
      }

      onPageAfterIn = () => {
        setTimeout(function(){
          console.log("Action")
          this.props.changeFooterNavbarShow(this.state.showBottomNav);
          this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
      
          if(this.props.isAuthenticated){
            this.$f7.view[0].router.navigate('/');
          }
          else {
            // this.$f7.view[0].router.navigate('/');
          }
        }.bind(this), 10);
      }

    render() {
        let { open} = this.state;
        return (
            <Page className="forgotpw" onPageAfterIn={this.onPageAfterIn}>
                {
                    (this.state.loading) ?
                        <div className="loading"></div>
                        :
                        <div>
                            <Modal closeOnOverlayClick={false}  modalId="signupPopup" closeIconId="closeSignupPop" open={open} onClose={this.onCloseModal} center>
                            {
                                this.state.status == "success" ?
                                <Link className="successSignup" iconIos="f7:checkmark_alt_circle" iconMd="material:check_circle_outline" href="#" />
                                :
                                <Link className="errorSignup" iconIos="f7:info_circle_fill" iconMd="material:error" href="#" />
                            }
                            
                            <h2>{this.state.message}</h2>
                            <Link className="fw-login" href={`/reset-password/user/`+this.state.customerId+`/`} onClick={()=>this.onCloseModal()} animate={false}>Reset Password</Link>
                            </Modal>

                            <Navbar className="transparent pw-reset-nav" noShadow={true} noHairline={true}>
                                <NavLeft backLink>
                                    {/* <Link href='/Login/' iconIos="f7:chevron_left" iconMd="material:arrow_back" /> */}
                                </NavLeft>
                            </Navbar>
                            <div className="login-wrapper">
                                <div className="sign-in-logo">
                                    <img src='../static/speedonx-logo.svg' />
                                </div>
                                <h3 className="login-wrapper-title">Verify Code</h3>
                                <div className="auth-sing-info">
                                    <List form>
                                        <ListInput
                                            type="password"
                                            placeholder="Verification Code"
                                            value={this.state.vcode}
                                            name="vcode"
                                            onChange={this.changeHandler}
                                        ></ListInput>
                                        
                                        <div className="auth-sing-btn-wrapper">
                                            <div className="forgot-link-pws">
                                                {/* <Link href="/Login/"><i className='far fa-arrow-left'></i> Login</Link> */}
                                            </div>
                                            <Button className="auth-sing-btn" type="submit" onClick={(e) => this.onSubmit(e)}>
                                                {this.state.pleaseWait ? <i class="fa fa-spin fas fa-spinner"></i> : <i className='far fa-arrow-right'></i>}
                                            </Button>
                                        </div>
                                    </List>
                                </div>

                            </div>
                        </div>
                }
            </Page>
        );
    }
}
function mapStateToProps(state) {
    return {
      isAuthenticated: state.auth.isAuthenticated,
      setting: state.setting
    }
  }

const mapDispachToProps = (dispach) => {
    return {
        changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
        changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
    }
}

export default connect(mapStateToProps, mapDispachToProps)(ForgetPassword);