import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from './../Store/actions/actionTypes';
import { AJAX_PUBLIC_REQUEST } from '../Constants/AppConstants';
import ReactImageFallback from 'react-image-fallback';
import { RandomLoader } from './../pages/Common/ReactContentLoader';
import NoContent from './Common/NoContent';

class CategoriOffer extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,

      showBottomNav: true,
      showHeaderNav: "onlyback",

      categoryList:[]
    }
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    this.getCategoryOffer();
  }

  reduceStr = (str, size) => {
    if(str.length > 0){
      if(str.length > size) {
        str = str.substring(0, size)+'..';
      }
    }
    else {
      str = '---';
    }
    return str;
    
  }

  getCategoryOffer = () => {
    AJAX_PUBLIC_REQUEST("POST", "loyalty/getCategoryList", {}).then(results => {
        console.log(results.response.data);
        if (parseInt(results.response.code) === 1000) {
            this.setState({
                loading: false,
                categoryList: results.response.data,
            });
        } else {
          this.setState({
            loading: false,
            categoryList: [],
          });
        }
    });
  }

onPageAfterIn = () => {
  //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
  // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
  setTimeout(function(){
    console.log("Action")
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
  }.bind(this), 10);
}


  render() {
    let categories = this.state.categoryList;
    return (
      this.state.loading ?
      <Frc.Page name="" className="p-b-45vw myOrderPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Frc.Page name="" className="p-b-45vw myOrderPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <Fragment>
          <div className="CategoriOffer-wrapper">
          <h3 className="inner-title">CATEGORIES</h3>

          { 
            categories.length > 0 ?
            categories.map((item, index) => {
                return (
                    <div key={index} className="category-offer-list">
                        <Frc.Link href={`/LoyaltyOffer/${item.id}`}>
                          <div>
                            <div className="category-offer-list-img">
                                <ReactImageFallback
                                  src={`${item.photo}`}
                                  fallbackImage='./../static/placeholder_dark.svg'
                                  initialImage='./../static/placeholder_dark.svg'
                                  alt='image'
                                  className=""
                                />

                            </div>
                            <h3>{this.reduceStr(item.name, 15)}</h3>
                          </div>
                        </Frc.Link>
                    </div>
                );
            })
            :
            <div className="notification-details" style={{width: '100%'}}>
                <NoContent icon = 'far fa-frown' text="No Offer Found!" />
            </div>
          
          }

          </div>
        </Fragment>
      </Frc.Page >
    );
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(null, mapDispachToProps)(CategoriOffer);