import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import ReactImageFallback from 'react-image-fallback';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { RandomLoader } from '../Common/ReactContentLoader';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, GET_STORAGE, SET_STORAGE, ITEM_COUNT } from "../../Constants/AppConstants";
import InfiniteScroll from 'react-infinite-scroller';
import NoContent from '../Common/NoContent';



class Tips extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "onlyback",

      tips: [],

      errorMessage: '',

      hasMoreItems: true,
      offset: 0,

      total_items: 0,


    }
    document.title = `Tips - ${SITE_TITLE}`;
  }



  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    this.loadMore();
  }

  reduceStr = (str, size) => {
    if (str.length > 0) {
        if (str.length > size) {
            str = str.substring(0, size) + '..';
        }
    }
    else {
        str = '---';
    }
    return str;

}


  mergeArray = (arr1, arr2) => {
    const combined = Object.values(arr1.concat(arr2).reduce((r, o) => {
        r[o.id] = o;
        return r;
    }, {}));
    return combined;
  } 

  loadMore = (offset=null) => {
    AJAX_PUBLIC_REQUEST("POST", "home/tipsAndTricks", {
        offset: offset ? offset : this.state.offset,
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {

            let tips = [];
            tips = this.mergeArray(this.state.tips, results.response.data);

            if (results.response.data.length > 0) {
                if (this.state.hasMoreItems) {
                    if (parseInt(results.response.offset) < results.response.total_items) {
                        this.setState({
                            tips: tips,
                            offset: parseInt(results.response.offset) + 10,
                            total_items: parseInt(results.response.total_items)
                        });
                    }
                    else if (parseInt(results.response.offset) == results.response.total_items) {
                        this.setState({
                            tips: tips,
                            hasMoreItems: false
                        });
                    }
                    else {
                        this.setState({
                            hasMoreItems: false
                        });
                    }
                }

            } else {
                this.setState({
                    loading: false,
                    hasMoreItems: false,
                    tips: tips,
                })
            }

        } else {
            this.setState({
                hasMoreItems: false,
                offset: 0,
                total_items: 0
            })
        }

        if (this.state.loading) {
            this.setState({
                loading: false,
            });
        }
    });
  }

  reduceStr = (str, size) => {
    if (str.length > 0) {
        if (str.length > size) {
            str = str.substring(0, size) + '..';
        }
    }
    else {
        str = '---';
    }
    return str;

  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  onPtrRefresh = (done) => {
    setTimeout(() => {
      this.loadMore(0);
      done();
    }, 1000);
  }

  render() {
    return (
      this.state.loading ?
        <Frc.Page name="detailsbike" className="" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}
        infinite
        infiniteDistance={50}
        infinitePreloader={this.state.hasMoreItems}
        onInfinite={() => this.loadMore()}
        >
          <RandomLoader />
        </Frc.Page>
        :
        <Fragment>
          <Frc.Page name="detailsbike" className="" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}
          infinite
          infiniteDistance={50}
          infinitePreloader={this.state.hasMoreItems}
          onInfinite={() => this.loadMore()}
          >
            <div className="CategoriOffer-wrapper">
              <h3 className="inner-title">Tips &amp; Tricks</h3>
              {
                this.state.tips.length > 0 ?
                  // <InfiniteScroll
                  //   pageStart={1}
                  //   loadMore={() => this.loadMore()}
                  //   hasMore={this.state.hasMoreItems}
                  //   loader={<p key={Math.random()} style={{ width: '100%', textAlign: 'center' }}><i className="fas fa-spinner fa-spin fa-2x fa-fw"></i></p>}
                  //   useWindow={false}
                  // >
                    <Frc.Block className="product-details-wrapper">
                        {
                          this.state.tips.map((tip, index) =>
                          <div key={index} style={{ padding: '0 10px' }}>
                            <Frc.Link className="no-ripple" href={`/TipDetails/tipsntricks/` + tip['id'] + `/`} noFastClick style={{marginBottom: '10px'}}>
                                <Frc.Row>
                                    <Frc.Col width="40">
                                        <div className="categories-thumb ripple" style={{backgroundColor: '#000'}}>
                                            <ReactImageFallback
                                                src={tip['image']}
                                                fallbackImage='./../static/placeholder_dark.svg'
                                                initialImage='./../static/placeholder_dark.svg'
                                                alt='image'
                                                className="tipsImage"
                                            />
                                        </div>
                                    </Frc.Col>
                                    <Frc.Col width="60">
                                        <div className="categories-details" style={{textAlign: 'left'}}>
                                            {/* <div className="name">{this.reduceStr(tip['title'], 10)}</div> */}
                                            <div className="name" style={{fontSize: '4.5vw', lineHeight: '7.5vw', marginBottom: '0vw'}}>{this.reduceStr(tip['title'], 40)}</div>
                                            <div style={{fontSize: '3vw'}}>
                                              <i style={{transform: 'scale(-1, 1)', paddingLeft: '5px'}} className="fa fa-pencil"></i> {tip['publish_date']}
                                            </div>
                                            <div className="book-now view-more-tip">
                                                View More
                                            </div>
                                        </div>
                                    </Frc.Col>
                                </Frc.Row>
                            </Frc.Link>
                          </div>
                          )
                        }
                    </Frc.Block>
                  // </InfiniteScroll>
                  :
                  <Frc.Block className="product-details-wrapper">
                        <div className="notification-details" style={{width: '100%'}}>
                            <NoContent icon = 'far fa-frown' text="No Tips and Tricks Found!" />
                        </div>
                  </Frc.Block>
              }
            </div>
          </Frc.Page>
        </Fragment>
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(Tips);