import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import { AJAX_PUBLIC_REQUEST } from '../Constants/AppConstants';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../Store/actions/actionTypes';


import { RandomLoader } from './../pages/Common/ReactContentLoader';
import ReactImageFallback from 'react-image-fallback';

import NoContent from './Common/NoContent';



class LoyaltyOffer extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "back",
      categoryId: this.$f7route.params.categoryId,

      offers: [],
    }
  }
  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    this.getLoyaltyOffer(this.state.categoryId);
  }
  
  reduceStr = (str, size) => {
    if(str.length > 0){
      if(str.length > size) {
        str = str.substring(0,size)+'..';
      }
    }
    else {
      str = '---';
    }
    return str;
    
  }

  getLoyaltyOffer = (id) => {
    AJAX_PUBLIC_REQUEST("POST", "loyalty/getCategoryLoyaltyList", {loyalty_cat_id:id}).then(results => {
        console.log(results);
        if (parseInt(results.response.code) === 1000) {
            this.setState({
                loading: false,
                offers: results.response.data,
            });
        } else {
            this.setState({
                loading: false,
                errorMessage: results.response.message,
            })
        }
    });
  }

  goToLink = (e, url) => {
    // e.preventDefault();
    if(url){
      const self = this;
      self.$f7.preloader.show('red');
      // window.location = url;
    }
    else {
      this.showToastWithButton("Link is not Available!")
    }
    
    // setTimeout(() => {
    //   self.$f7.preloader.hide();
    // }, 2000);
  }

  showToastWithButton(text) {
    const self = this;
    // Create toast
    if (!self.toastWithButton) {
      self.toastWithButton = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 1000,
      });
      self.toastWithButton.open();
    }
    else {
      self.toastWithButton.close();
      self.toastWithButton.destroy();
      self.toastWithButton = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 1000,
      });
      self.toastWithButton.open();
    }
    
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  onPtrRefresh = (done) => {
    setTimeout(() => {
      this.getLoyaltyOffer(this.state.categoryId);
      done();
    }, 1000);
  }

  render() {
    return (
      this.state.loading ?
        <Frc.Page className="loyalty-offer" onPageAfterIn={this.onPageAfterIn} noSwipeback  ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
            <RandomLoader />
        </Frc.Page>
        :
      <Frc.Page className="loyalty-offer" onPageAfterIn={this.onPageAfterIn} noSwipeback style={{padding: '0 5px'}}  ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
        <Fragment>
          <div className="loyalty-wrapper">
            <h3 className="inner-title">Loyalty Offers</h3>
            <Frc.Row >
              {
                this.state.offers ?
                  this.state.offers.length > 0 ?
                  this.state.offers.map((offer, index) =>
                  <Frc.Col key={"loyalty"+ index} className="loyalty-brand" width="33">
                    <div className="loyalty-brand-img">
                      {/* <img src='../static/loyalty/01.png' /> */}
                      <ReactImageFallback
                          src={offer['photo']}
                          fallbackImage='./../static/placeholder_dark.svg'
                          initialImage='./../static/placeholder_dark.svg'
                          alt='image'
                          className=""
                      />
                    </div>
                    <div className="loyalty-brand-info">
                      <h3>{this.reduceStr(offer['title'], 10)}</h3>
                      <span>{offer['discount']}%</span>
                    </div>
                    {
                      offer['url_link'] ?
                      <Frc.Link external className="LoyaltyCardLink" onClick={(e)=>this.goToLink(e, offer['url_link'])} href={offer['url_link']}> </Frc.Link>
                      :
                      <Frc.Link className="LoyaltyCardLink" onClick={(e)=>this.goToLink(e, offer['url_link'])} href="#"> </Frc.Link>
                    }
                    
                    {/* <Frc.Link external className="LoyaltyCardLink" onClick={(e)=>this.goToLink(e, offer['url_link'])} href={offer['url_link']}> </Frc.Link> */}
                  </Frc.Col>
                  )
                  :
                  // <p>No Offers Available!</p>
                  <div className="notification-details" style={{width: '100%'}}>
                      <NoContent icon = 'far fa-frown' text="No Loyalty Offer Found!" />
                  </div>
                  :
                  ''
              }
            </Frc.Row>
          </div>
        </Fragment>
      </Frc.Page >
    );
  }
}

// export default BikeHome;
const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(null, mapDispachToProps)(LoyaltyOffer);
