import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, GET_STORAGE, SET_STORAGE, ADD_TO_CART, ITEM_COUNT } from "./../../Constants/AppConstants";
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import ReactImageFallback from 'react-image-fallback';

class SingleItem extends Component {

    constructor(props) {
        super(props)

        this.state = {
            item_on_cart: 0
        }
    }

    componentDidMount() {
        if (ITEM_COUNT() > 0) {
            const cart = JSON.parse(GET_STORAGE("cart"));
            cart.forEach(function (item, key) {
                if (item.id == this.props.product.id) {
                    this.setState({
                        item_on_cart: item.quantity
                    })
                }
            }.bind(this))
        }
    }

    //=== handle Cart == //
    handleCart = (e, type, product) => {
        e.preventDefault();
        let item_on_cart = 0;
        if (type == 'add') {
            item_on_cart = this.state.item_on_cart + 1;
        }
        if (type == 'substruct') {
            item_on_cart = this.state.item_on_cart <= 0 ? 0 : this.state.item_on_cart - 1;
        }
        if (type == 'delete') {
            item_on_cart = 0;
        }
        this.setState({
            item_on_cart
        });

        ADD_TO_CART(product, type, item_on_cart);
        this.props.handel_cart_state();
    }



    render() {
        const item = this.props.product;
        return (
            <Fragment>
                <tr>
                    <td className="cart-item-img" style={{width: 'unset'}}>
                        {/* <img src={product['photo']['image']} /> */}
                        <ReactImageFallback
                            src={item['photo']['image']}
                            fallbackImage='./../static/placeholder_dark.svg'
                            initialImage='./../static/placeholder_dark.svg'
                            alt='image'
                            className="swiper-lazy"
                        />
                    </td>
                    <td className="cart-product-name" style={{width: 'unset'}}>
                        <span className="display-block">{item['title']}</span>
                        {/* <span className="display-block">Tk. {item['price']} {(item['discounted_price']!= '' && parseInt(item['price'])!=parseInt(item['discounted_price'])) ? <span style={{textDecoration: 'line-through', opacity: 0.6}}>{parseInt(item['price'])+parseInt(item['discounted_price']!= '' ? item['discounted_price'] : 0)}</span> : ''}</span> */}
                        <span className="display-block">Tk. {parseInt(item['discounted_price']) > 0 ? item['discounted_price'] : item['price']} 
                        {parseInt(item['discounted_price']) > 0 ? <span style={{textDecoration: 'line-through', opacity: 0.6, marginLeft: '5px'}}>{ parseInt(item['price']) > 0 ? item['price'] : 0 }</span> : ''}</span>
                    </td>
                    <td className="cart-product-qnt" style={{width: 'unset'}}>
                        <span onClick={(e) => this.handleCart(e, 'add', item)}><i className="fas fa-chevron-up"></i></span>
                        <input className="inputne" readOnly value={item['quantity']}></input>
                        <span onClick={(e) => this.handleCart(e, 'substruct', item)}><i className="fas fa-chevron-down"></i></span>
                    </td>
                    <td className="cart-product-dlt" onClick={(e) => this.handleCart(e, 'delete', item)} style={{textAlign: 'right', width: 'unset'}}>
                        <i className="fas fa-trash-alt"></i>
                    </td>
                </tr>

                {/* <li>
                    <div className="cart-item-img">
                        <ReactImageFallback
                            src={item['photo']['image']}
                            fallbackImage='./../static/placeholder_dark.svg'
                            initialImage='./../static/placeholder_dark.svg'
                            alt='image'
                            className="swiper-lazy"
                        />
                    </div>
                    <div className="cart-product-name">
                        <span className="display-block">{item['title']}</span>
                       <span className="display-block">Tk. {parseInt(item['discounted_price']) > 0 ? item['discounted_price'] : item['price']} 
                        {parseInt(item['discounted_price']) > 0 ? <span style={{textDecoration: 'line-through', opacity: 0.6, marginLeft: '5px'}}>{ parseInt(item['price']) > 0 ? item['price'] : 0 }</span> : ''}</span>
                    </div>
                    <div className="cart-product-qnt">
                        <span onClick={(e) => this.handleCart(e, 'add', item)}><i className="fas fa-chevron-up"></i></span>
                        <input className="inputne" readOnly value={item['quantity']}></input>
                        <span onClick={(e) => this.handleCart(e, 'substruct', item)}><i className="fas fa-chevron-down"></i></span>
                    </div>
                    <div className="cart-product-dlt" onClick={(e) => this.handleCart(e, 'delete', item)}>
                        <i className="fas fa-trash-alt"></i>
                    </div>
                </li> */}
            </Fragment>
        );
    }
}


export default SingleItem;