import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
// import FooterTabs from './FooterTabs';
import { connect } from 'react-redux';
import { GET_STORAGE, USER, AJAX_REQUEST, AJAX_PUBLIC_REQUEST } from './../../Constants/AppConstants';
import { Router } from 'react-router';
import Modal from 'react-responsive-modal';
let user_info = '';

class FooterNav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      notifications: [],
      open: false,
      links: [],
      active: ''
    }
  }
  componentDidMount() {
    this.getSocialLinks();
    if(this.props.isAuthenticated){
      user_info = GET_STORAGE(USER);
      this.getNotifications();
      this.coundownNotification(); 
    }   
    else {
      this.setState({
        notifications: []
      })
    }
  }


  getSocialLinks = () => {
    AJAX_PUBLIC_REQUEST('POST','home/socialLinks',{
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            links: results.response.data
          })
        }
      });
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };
 
  onCloseModal = () => {
    this.setState({ open: false });
  };

  componentDidUpdate(prevProps, prevState) {
    if (this.props.isAuthenticated != prevProps.isAuthenticated && !this.props.isAuthenticated) {
        user_info = GET_STORAGE(USER);
        this.setState({
            notifications: [],
        })
    }
    else if(this.props.isAuthenticated != prevProps.isAuthenticated && this.props.isAuthenticated) {
      user_info = GET_STORAGE(USER);
      this.getNotifications();
      this.coundownNotification(); 
    }
}

  checkNotification = () => {
    // {
    //   this.props.isAuthenticated ?
    //   ""
    //   :
    //   this.showToastIcon("Please Login First!")
    // }
    if(!this.props.isAuthenticated){
      this.showToastIcon("Please Login First!");
      let self = this;
      setTimeout(function(){
        self.$f7.view[0].router.navigate('/Login/');
       }, 550);
    }
  }

  getNotifications = () => {
    if (user_info) {
      AJAX_REQUEST('POST','setting/getCountUnreadNotificaton',{
        customer_id: JSON.parse(user_info)['id']
      }).then(results => {
          if (parseInt(results.response.code) === 1000) {
            this.setState({
              notifications: results.response.data
            })
          }
        });
    }
  }

  coundownNotification = () => {
    if (user_info) {
      setInterval(function(){ 
        AJAX_REQUEST('POST','setting/getCountUnreadNotificaton',{
          customer_id: JSON.parse(user_info)['id']
        }).then(results => {
            if (parseInt(results.response.code) === 1000) {
              this.setState({
                notifications: results.response.data
              })
            }
          });
      }.bind(this), 30000);
    }
  }

  showToastIcon(text) {
    const self = this;
    // Create toast
    if (!self.toastIcon) {
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 500,
        closeButton: true,
      });
      self.toastIcon.open();
    }
    else {
      self.toastIcon.close();
      self.toastIcon.destroy();
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 500,
        closeButton: true,
      });
      self.toastIcon.open();
    }
  }


  callNumber = (e, number) => {
    console.log(number)
    // this.inputElement.click();
    if (typeof AndroidInterface != "undefined") {
      AndroidInterface.showDialPad(number);
      this.setState({ open: false });
    } else {
      this.setState({ open: false });
      window.webkit.messageHandlers.callOption.postMessage(number);
    }
  }


  render() {
    const { open } = this.state;
    const {active} =this.props;
    return (
      <Fragment>
        <Modal modalId="signupPopup" closeIconId="closeSignupPop" open={open} onClose={this.onCloseModal} center>
          {
            this.state.status == "success" ?
            <Frc.Link className="successSignup" iconIos="f7:checkmark_alt_circle" iconMd="material:check_circle_outline" href="#" />
            :
            <Frc.Link className="errorSignup" iconIos="f7:info_circle_fill" iconMd="material:error" href="#" />
          }
          
          <Frc.Block className="hotline" popupOpen>
                  <Frc.Button className="call-btn"> Hotline </Frc.Button>
                  <p className="hotline-number">{this.state.links ? this.state.links['contact_mobile'] : 'Number not Available'}</p>
                  <div className="hotline-number-call">
                    <Frc.Button onClick={this.onCloseModal}> Cancel </Frc.Button>
                    <Frc.Button onClick={(e)=>this.callNumber(e, this.state.links ? this.state.links['contact_mobile'] : '#')}>Call</Frc.Button>
                  </div>
                </Frc.Block>
        </Modal>


          <Frc.Link href="/home/" tabLinkActive={(active.includes("home") || active.includes("/?device_uuid=") || active==("/")) ? true : false}>
            <span className="icon-footer-nav home"></span>
          </Frc.Link>

          {/* <Frc.Link href="/list-view/"> */}
          {/* <Frc.Link href="#" raised popupOpen="#contact-popup" tabLink="#popup-hotline"> */}
          <Frc.Link href="#" onClick={this.onOpenModal}>
            <span className="icon-footer-nav call"></span>
          </Frc.Link>

          {/* <Frc.Link href="/request-and-load/"> */}
          <Frc.Link href="/category/" tabLinkActive={active.includes("category") ? true : false}>
            <span className="icon-footer-nav catagories"></span>
          </Frc.Link>

          <Frc.Link href="/SubscriptionForm/" tabLinkActive={active.includes("SubscriptionForm") ? true : false}>
            <span className="icon-footer-nav forms"></span>
          </Frc.Link>

          <Frc.Link  onClick={this.checkNotification} href={this.props.isAuthenticated ? "/notifications/" : '#'} tabLinkActive={active.includes("notifications") ? true : false}>
            {
              this.state.notifications > 0 ?
              // this.state.notifications.length > 0 ?
              <span className="icon-footer-nav bell">
                <span className="footer-notifications">{this.state.notifications}</span>
              </span>
              :
              <span className="icon-footer-nav bell">
              </span>
              // :
              // ''
            }
          </Frc.Link>

      </Fragment>
    );
  }
}

// export default FooterNav;

function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

export default connect(mapStateToProps, null)(FooterNav);