import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import ReactImageFallback from 'react-image-fallback';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, GET_STORAGE, SET_STORAGE, ADD_TO_CART, ITEM_COUNT } from "./../../Constants/AppConstants";

import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';


const params_0 = {
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets',
    clickable: true
  },
  slidesPerView: 1,
  spaceBetween: 0,
  loop: true,
  // speed: 1200,
  // autoplay: {
  //   delay: 2500,
  //   disableOnInteraction: false
  // },
};

class BikeDetails extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "false",

      productId: this.$f7route.params.productId,

      productData: [],
      photos: [],

      errorMessage: '',

      search: '',
      itemFound: false,
      searchResult: [],
    }
    document.title = `Oil Details - ${SITE_TITLE}`;
  }

  clearSearch = () => {
      this.setState({
          itemFound: false
      },()=> {
          this.processSearch();
        })
  }

  searchAll = (e) => {
      const { value } = e.target;
      if(value.length > 3){
          this.setState({
              itemFound: true
          },()=> {
              this.processSearch(value);
            })
      }
      else {
          this.setState({
              itemFound: false
          },()=> {
              this.processSearch(value);
            })
      }
      
  }

  processSearch = (value='') => {
      this.state.itemFound ?
      AJAX_PUBLIC_REQUEST("POST", "product/getTagwiseProducts",{
          tag: value,
          offset: 0,
      }).then(results => {
          if (parseInt(results.response.code) === 1000) {
                this.setState({
                  searchResult: results.response.data
                })
                this.searchPopover.open()
          } else {
              this.setState({
                  searchResult: []
                })
              this.searchPopover.open()
          }
        })
      :
      this.searchPopover.close()
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
        module: "product",
        page_name: "details",
        content_id: this.state.productId,
        category_id: 0,
      }).then(results => {            
    });
    this.getOilDetails();
  }

  getOilDetails = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getProductDetails", {
      product_id: this.state.productId,
      category_id: 2,
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          loading: false,
          productData: results.response.data,
          photos: results.response.data.photo
        });
      } else {
        this.setState({
          loading: false,
          errorMessage: results.response.message,
        })
      }
    });
  }

  last_page = () => {
    let last_link = this.$f7.view[0].router.previousRoute.url ? this.$f7.view[0].router.previousRoute.url : '/home/';     
    if(last_link.includes("Details")){
      last_link = "/list-oil/brand/"+this.state.productData.brand_id+`/`;
    }  
    return last_link;
  }

  handleCart = (e, type, product) => {
    e.preventDefault();
    let item_on_cart = 0;
    if (type == 'add') {
        item_on_cart = this.state.item_on_cart + 1;
    }
    if (type == 'substruct') {
        item_on_cart = this.state.item_on_cart <= 0 ? 0 : this.state.item_on_cart - 1;
    }
    if (type == 'delete') {
        item_on_cart = 0;
    }
    this.setState({
        item_on_cart
    });

    ADD_TO_CART(product, type, item_on_cart);
    this.$f7router.navigate('/Cart/');

    // history.push('/#/Cart/');
    // location.reload(); 
}

processSearch = (value='') => {
  this.state.itemFound ?
  AJAX_PUBLIC_REQUEST("POST", "product/getTagwiseProducts",{
      tag: value,
      offset: 0,
  }).then(results => {
      if (parseInt(results.response.code) === 1000) {
            this.setState({
              searchResult: results.response.data
            })
            this.searchPopover.open()
      } else {
          this.setState({
              searchResult: []
            })
          this.searchPopover.open()
      }
    })
  :
  this.searchPopover.close()
}

goToProduct = (e, type, id) => {
  e.preventDefault();
  let self = this;
  console.log(self.searchBar)
  self.searchBar.disable();
  setTimeout(function(){ 
      {
              type == 1 ? 
              self.$f7.view[0].router.navigate('/BikeDetails/product/'+id+'/')                
              :
              self.$f7.view[0].router.navigate('/OilDetails/product/'+id+'/')       
      }
   }, 1000);
  
}

enableSearch = () => {
  this.searchBar.enable();
  this.searchBar.clear();
}

onPageAfterIn = () => {
  //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
  // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
  setTimeout(function(){
    console.log("Action")
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
  }.bind(this), 10);
}

onPageBeforeIn = () => {
  setTimeout(function(){
    console.log("Action 1st")
    this.props.changeFooterNavbarShow(false);
    this.props.changeHeaderNavbarShow('hide');
  }.bind(this), 10);
}

onPtrRefresh = (done) => {
  setTimeout(() => {
    this.getOilDetails();
    done();
  }, 1000);
}


  render() {
    return (
      this.state.loading ?
      <Frc.Page className="product-details-content" 
      onPageBeforeIn={this.onPageBeforeIn}
      onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
        <RandomLoader /> 
      </Frc.Page>
      :
      // <Fragment>
        <Frc.Page name="detailsbike" className="product-details-content" 
        onPageBeforeIn={this.onPageBeforeIn}
        onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
          <Frc.Navbar className="transparent" noShadow={true} noHairline={true}>
            <Frc.NavLeft backLink>
              {/* <Frc.Link href={this.last_page()} iconIos="f7:chevron_left" iconMd="material:arrow_back" /> */}
            </Frc.NavLeft>
            <Frc.NavTitle></Frc.NavTitle>
            <Frc.NavRight>
              {/* <Frc.Link searchbarEnable=".searchbar-demo" iconIos="f7:search" iconAurora="f7:search" iconMd="material:search"></Frc.Link> */}
              <Frc.Link onClick={this.enableSearch} iconIos="f7:search" iconAurora="f7:search" iconMd="material:search"></Frc.Link>
              {
                ITEM_COUNT() > 0 ?
                <Frc.Link href="/Cart/" iconIos="f7:cart" iconAurora="f7:cart" iconMd="material:shopping_cart"><span className="cart-counter">{ITEM_COUNT()}</span></Frc.Link>
                : ''
              }
            </Frc.NavRight>
            {/* Search Input */}
            <Frc.Searchbar
              ref={input => this.searchBar = input} 
              className="searchbar-demo detailSearch"
              expandable
              // searchContainer=".search-list"
              // searchIn=".item-title"
              name="search"
              onChange={this.searchAll}
              onClickClear={this.clearSearch}
              onClickDisable = {this.clearSearch}
            >
            </Frc.Searchbar>



                {/* Search Result */}
                <Frc.Popover className="popover-menu popover-on-bottom detail-page-popover" ref={popover => this.searchPopover = popover} style={{width: '100%', marginTop: '65px'}} backdrop={false} closeByOutsideClick={false} closeByBackdropClick={false} >
                
                    {
                        this.state.searchResult ?
                        this.state.searchResult.length > 0 ?
                        <Frc.List mediaList>
                        {
                            this.state.searchResult.map((searchRes, index) =>                         
                                // <Frc.ListItem link="#" popoverClose title="Dialog" /> 
                                <Frc.ListItem
                                    popoverClose
                                    onClick = {(e)=>this.goToProduct(e, searchRes['category_id'], searchRes['id'])}
                                    className = 'searchresult'
                                    key={`search`+index}
                                    // link={
                                    //         searchRes['category_id'] == 1 ? 
                                    //         '/BikeDetails/product/'+searchRes['id']+'/'
                                    //         :
                                    //         '/OilDetails/product/'+searchRes['id']+'/'
                                    // }
                                    link = "#"
                                    view=".view-main"
                                    title={searchRes['title']}
                                    // after={`৳`+searchRes['price']}
                                    after={parseInt(searchRes['discount']) > 0 ? `৳`+searchRes['discount'] : `৳`+searchRes['price']}
                                    subtitle={searchRes['tag']}
                                    text={searchRes['category_id'] == 1 ? 'Bike' : 'Oil'}
                                >
                                    {
                                    searchRes['category_id'] == 1 ? 
                                    <ReactImageFallback
                                        src={searchRes['photo']}
                                        fallbackImage='./../static/placeholder_dark_big.svg'
                                        initialImage='./../static/placeholder_dark_big.svg'
                                        alt='image'
                                        width='80'
                                        style={{maxWidth: '80px'}}
                                        slot="media"
                                    />
                                    
                                    : 
                                    <ReactImageFallback
                                        src={searchRes['photo']}
                                        fallbackImage='./../static/placeholder_dark_big.svg'
                                        initialImage='./../static/placeholder_dark_big.svg'
                                        alt='image'
                                        width='80'
                                        style={{maxWidth: '80px'}}
                                        slot="media"
                                    />
                                    }
                                </Frc.ListItem>                       
                            )
                        }
                        </Frc.List>
                        : 
                        <Frc.List style={{textAlign: 'center'}}>
                            <Frc.ListItem className="no-result" link="#" popoverClose title="No Result Found!" />
                        </Frc.List>
                        :
                        ''
                    }
                    
                </Frc.Popover>
          </Frc.Navbar>
          <Frc.Block className="product-details-wrapper">
          {
            this.state.photos.length > 0 ? 
            <div className="product-details-container">
              <Swiper {...params_0} loop={this.state.photos.length > 1 ?  false : false }>
              
                    {this.state.photos.map((image, index) =>
                      <div key={index} className="product-details">
                        {/* <img src={image['image']}/> */}
                        <ReactImageFallback
                          src={image['image']}
                          fallbackImage='./../static/placeholder_dark_big.svg'
                          initialImage='./../static/placeholder_dark_big.svg'
                          alt='image'
                          className="swiper-lazy"
                        />
                        {/* <div className="swiper-lazy-preloader swiper-lazy-preloader-white" /> */}
                      </div>
                    )}
                    
              </Swiper>
            </div>
            : 
            ''
          }


            <Frc.Block className="product-details-price">
              <div className="brand-name">{this.state.productData ? this.state.productData.title : '---'}</div>
              <div className="brand-engine-capacity">Engine Grade : {this.state.productData ? this.state.productData.engine_grade ? this.state.productData.engine_grade : '---' : '---'}</div>
              <div className="brand-name-price">Tk. {this.state.productData ? parseInt(this.state.productData.discounted_price) > 0 ? this.state.productData.discounted_price : this.state.productData.price : '---'} {this.state.productData ? parseInt(this.state.productData.discounted_price) > 0 ? <span>Tk. {this.state.productData ? this.state.productData.price ? this.state.productData.price : '0' : '---'}</span> : '' : '---'}</div>
            </Frc.Block>

            {
              this.state.productData ?
                (this.state.productData.has_insurance == "yes") || (this.state.productData.has_loyalty == "yes") ?
                  <Frc.Block className="product-details-insurance">
                    {
                      this.state.productData ? this.state.productData.has_insurance == "yes" ?
                        <div className="brand-insurance">Free Insurance</div>
                        :
                        ''
                        :
                        ''
                    }
                    {
                      this.state.productData ? this.state.productData.has_loyalty == "yes" ?
                        <div className="brand-loyalty-card">Loyalty Card</div>
                        :
                        ''
                        :
                        ''
                    }
                  </Frc.Block>
                  :
                  ''
                :
                ''
            }

            {/* Product details Overview */}
            {
              this.state.productData ?
              <Frc.Block className="product-details-overview">
                <div className="block-title">Product Details</div>
                <div className="data-table">
                  <span dangerouslySetInnerHTML={{ __html: (this.state.productData.details ? this.state.productData.details : '') }}></span>
                </div>
              </Frc.Block>
              : ''
            }

            {/* End Product details Overview */}
            {
              this.props.isAuthenticated ?
              // <Frc.Link className="product-book-bnt" onClick={(e) => this.handleCart(e, 'add', this.state.productData)} href={`/Cart/`}>Order Now</Frc.Link>
              <Frc.Link className="product-book-bnt" onClick={(e) => this.handleCart(e, 'add', this.state.productData)}>Order Now</Frc.Link>
              :
              // <Frc.Link className="product-book-bnt" href={`/Checkout/product/`+this.state.productId+`/`}>Order Now</Frc.Link>
              <Frc.Link className="product-book-bnt" onClick={(e) => this.handleCart(e, 'add', this.state.productData)}>Order Now</Frc.Link>
            }
          </Frc.Block>

        </Frc.Page>
      // </Fragment>
    );
  }
}

// export default BikeHome;
function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(BikeDetails);