import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { logout } from '../../Store/actions/loginActions';
import PropTypes from 'prop-types';

import {
    App,
    Panel,
    Views,
    View,
    Popup,
    Page,
    Navbar,
    Toolbar,
    NavRight,
    Link,
    Block,
    BlockTitle,
    LoginScreen,
    LoginScreenTitle,
    List,
    ListItem,
    ListInput,
    ListButton,
    BlockFooter,
    Button, Row, Col, NavLeft, Icon
  } from 'framework7-react';

class Logout extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {}
    }

    makeLogout = (e) => {
        e.preventDefault();
        this.props.logout();
        this.showToastIcon("Logged Out");
    }

    showToastIcon(text) {
        const self = this;
        // Create toast
        if (!self.toastIcon) {
          self.toastIcon = self.$f7.toast.create({
            icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
            text: text,
            position: 'center',
            closeTimeout: 3000,
          });
          self.toastIcon.open();
        }
        else {
          self.toastIcon.close();
          self.toastIcon.destroy();
          self.toastIcon = self.$f7.toast.create({
            icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
            text: text,
            position: 'center',
            closeTimeout: 3000,
          });
          self.toastIcon.open();
        }
    }

    render() {
        return (
            <Link className="nav-logout" panelClose onClick={(e)=>this.makeLogout(e)} href='/' >Logout</Link>
        );
    }
}


Logout.propTypes = {
    logout: PropTypes.func.isRequired,
}

export default connect(null, { logout })(Logout);
