import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import ReactImageFallback from 'react-image-fallback';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { RandomLoader } from '../Common/ReactContentLoader';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, GET_STORAGE, SET_STORAGE, ITEM_COUNT } from "../../Constants/AppConstants";
import NoContent from '../Common/NoContent';


class Terms extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: true,
      showHeaderNav: "show",
      terms: [],
      errorMessage: '',
    }
    document.title = `Terms - ${SITE_TITLE}`;
  }

  

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    this.getTerms();
  }


  getTerms = () => {
    // AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
    //   page_name: "bike",
    //   content_id: 0,
    //   module: "list",
    // }).then(results => {            
    // });
    AJAX_PUBLIC_REQUEST("POST", "home/termsCondition", {
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          loading: false,
          terms: results.response.data,
        });
      } else {
        this.setState({
          loading: false,
          errorMessage: results.response.message,
        })
      }
    });
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  onPtrRefresh = (done) => {
    setTimeout(() => {
      this.getTerms();
      done();
    }, 1000);
  }

  render() {    
    return (
      this.state.loading ?
      <Frc.Page name="detailsbike" className="product-details-content" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Fragment>
        <Frc.Page name="detailsbike" className="product-details-content" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
          <Frc.Block className="product-details-wrapper">
            <div className="CategoriOffer-wrapper" style={{paddingBottom: '44vw'}}>
              <h3 className="inner-title">{this.state.terms ? this.state.terms['pageTitle'] : ''}</h3>
              <Frc.Block className="product-details-overview" style={{textAlign: 'justify'}}>
                {/* <span dangerouslySetInnerHTML={{ __html: (this.state.terms ? this.state.terms['info'] : '') }}></span> */}
                  {
                    this.state.terms['info'] ?
                    <span dangerouslySetInnerHTML={{ __html: (this.state.terms ? this.state.terms['info'] : '') }}></span>
                    :
                    <div className="notification-details" style={{width: '100%'}}>
                        <NoContent icon = 'fa fa-list' text="No Content Found!" />
                    </div>
                  }
              </Frc.Block>
            </div>
          </Frc.Block>

        </Frc.Page>
      </Fragment>
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(Terms);