import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';


class Checkout extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showBottomNav: false,
      showHeaderNav: "show",

      productId: this.$f7route.params.productId,

      quantity: 1,
      show: true,
      max: 5,
      min: 0
    }
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  render() {
    return (
      <Frc.Page name="" className="p-b-45vw myCheckoutPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <Fragment>
          <div className="checkout-wrapper">
            <div className="checkout-list">
              <h3>Registered Customers</h3>
              <Frc.Link className="checkout-reg-btn" href="/login/">As a registered user</Frc.Link>
              <Frc.Link href="#">Sign In to quick checkout</Frc.Link>
            </div>
            <div className="checkout-list">
              <h3>Guest Checkuout</h3>
              {/* <Frc.Link className="checkout-reg-btn" href={`/CheckoutGuest/product/`+this.state.productId+`/`}>As a Guest</Frc.Link> */}
              <Frc.Link className="checkout-reg-btn" href={`/CheckoutGuest/`}>As a Guest</Frc.Link>
              <Frc.Link href={`/CheckoutGuest/`}>If you wish, you can register for an online account.</Frc.Link>
            </div>
          </div>
        </Fragment>
      </Frc.Page >
    );
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(null, mapDispachToProps)(Checkout);