import React, { Component, Fragment } from 'react';
import * as Frc  from 'framework7-react';

import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';

import { SITE_TITLE, AJAX_PUBLIC_REQUEST } from "./../../Constants/AppConstants";

import ReactImageFallback from 'react-image-fallback';
import { RandomLoader } from './../../pages/Common/ReactContentLoader';


import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';

const params = {
  pagination: '.swiper-pagination',
  effect: 'coverflow',
  grabCursor: true,
  centeredSlides: true,
  slidesPerView: 1.2,
  spaceBetween: -10,
  loop: true,
  lazy: true,
  speed: 1200,
  autoplay: {
    delay: 2500,
    disableOnInteraction: false
  },
  coverflow: {
    rotate: 50,
    stretch: 0,
    depth: 100,
    modifier: 1,
    slideShadows: true
  }
};
const params2 = {
  spaceBetween: 30,
  slidesPerView: 4.5,
  centeredSlides: false,
};

const params3 = {
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets',
    clickable: true
  },
  slidesPerView: 1,
  spaceBetween: 0,
  loop: false,
  // speed: 1200,
  // autoplay: {
  //   delay: 2500,
  //   disableOnInteraction: false
  // },
};

class OilHome extends Component {

  constructor(props){
    super(props)
    this.state = {
      loading: true,
      showBottomNav: true,
      showHeaderNav: "show",

      errorMessage: '',

      banners: [],
      brands: [],
      topfive: [],
      featured: [],
      advertisement: '',
    }
    document.title = `Oil - ${SITE_TITLE}`;
}
componentDidMount() {
  AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
    page_name: "oil",
    content_id: 0,
    module: "list",
  }).then(results => {            
  });
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);  
    this.getOilHomepage();
}

getOilHomepage = () => {
  AJAX_PUBLIC_REQUEST("POST", "dashboard/getOilHomepageData", {}).then(results => {
      if (parseInt(results.response.code) === 1000) {
          this.setState({
              loading: false,
              banners: results.response.data.bannerlist,
              brands: results.response.data.brandList,
              topfive: results.response.data.has_top_five,
              featured: results.response.data.is_featured,
              advertisement: results.response.data.advertisement,
          });
      } else {
          this.setState({
              loading: false,
              errorMessage: results.response.message,
          })
      }
  });
}

reduceStr = (str, size) => {
  if(str.length > 0){
    if(str.length > size) {
      str = str.substring(0,size)+'..';
    }
  }
  else {
    str = '---';
  }
  return str;
  
}

onPageAfterIn = () => {
  //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
  // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
  setTimeout(function(){
    console.log("Action")
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
  }.bind(this), 10);
}

onPtrRefresh = (done) => {
  setTimeout(() => {
    this.getOilHomepage();
    done();
  }, 1000);
}


  render() {
    return (
      this.state.loading ?
      <Frc.Page name="oil-home" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Frc.Page name="oil-home" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
        <Fragment>
          <div className="AppSlider">
            <Swiper 
              className="" {...params} 
              loop= {this.state.banners.length > 2 ? true : false}
            >
              {
              this.state.banners.length > 0 ?
              this.state.banners.map((banner_url, index) =>
              <a key={index} href={banner_url['target_url'] ? banner_url['target_url']: '#'}>
                <div>
                  {/* <ReactImageFallback
                      src={banner_url['photo']}
                      fallbackImage='./../../static/no-oil-image.png'
                      initialImage='./../../static/icon-loader.gif'
                      alt='image'
                      className=""
                  /> */}
                  <img
                    src='./../static/placeholder_banner_dark.svg'
                    data-srcset={banner_url['photo']}
                    // src={banner_url['photo']}
                    alt='image'
                    className="swiper-lazy"
                  />
                  <div className="swiper-lazy-preloader swiper-lazy-preloader-white" />
                </div>
                </a>
              )
              :
              ''
            }
            </Swiper>
          </div>

          {/** Subcribe Engine Oil **/}
          {
          this.state.advertisement != '' ?
          <Frc.Block className="no-margin no-padding subcribe-engine-oil">
            <h3 className="align-content-center">SUBSCRIBE FOR ENGINE OIL</h3>
            <Frc.Block className="no-margin no-padding subcribe-engine-oil-img">
              <a className="advertisement-oil" href={this.state.advertisement['advertisement_target'] ? this.state.advertisement['advertisement_target'] : '#'}>
                <ReactImageFallback
                    src={this.state.advertisement['oilAdvertisement']}
                    fallbackImage="./../../static/placeholder_dark_big.svg"
                    initialImage='./../../static/placeholder_dark_big.svg'
                    alt='image'
                    className="lazy"
                />
              </a>
              <Frc.Link href="/SubscriptionForm/" >SUBSCRIBE NOW</Frc.Link>
            </Frc.Block>
          </Frc.Block>
          :
          ''
        }
          {/** End Subcribe Engine Oil **/}

          {/* Brand Slider */}
          <div className="ProductSlider">
            <Frc.Swiper params={{ speed: 500, slidesPerView: 4.8, spaceBetween: 35 }}>
              {
                this.state.brands.length > 0 ?
                this.state.brands.map((brand, index) =>
                <Frc.SwiperSlide key={index}>
                  <div className="product-ctg-list-img">
                    <Frc.Link href={`/list-oil/brand/`+brand['id']+`/`}>
                      <ReactImageFallback
                        src={brand['photo']}
                        fallbackImage='./../static/placeholder_dark.svg'
                        initialImage='./../static/placeholder_dark.svg'
                        alt='image'
                        className=""
                      />
                    <span>{brand['name']}</span>
                    </Frc.Link>
                  </div>
                </Frc.SwiperSlide>
                )
                :
                ''
              }
            </Frc.Swiper>
          </div>
          {/* End Brand Slider */}


          {/* Book Slider */}
          <div className="book-slider">
            <Swiper
              className=""
              {...params3}
            >
              {
                this.state.topfive.length > 0 ?
                this.state.topfive.slice(0, 5).map((topfiveoil, index) =>
                <div key={index} className="book-slider-list">
                  <Frc.Link href={`/OilDetails/product/`+topfiveoil['id']+`/`}>
                    <div className="book-slider-img">
                      <ReactImageFallback
                        src={topfiveoil['photo']}
                        fallbackImage='./../static/placeholder_dark.svg'
                        initialImage='./../static/placeholder_dark.svg'
                        alt='image'
                        className=""
                      />
                    </div>
                    <div className="book-slider-list-content">
                      <span className="top-book">Top 5 Engine Oil</span>
                      <span className="top-book-title">{this.reduceStr(topfiveoil['title'], 15)}</span>
                      <span className="top-book-price">Tk: {topfiveoil['discount'] > 0 ? topfiveoil['discount'] : topfiveoil['price']}</span>
                      <span className="top-book-btn">ORDER NOW</span>
                    </div>
                  </Frc.Link>
                </div>
                )
                :
                ''
              }
            </Swiper>
       
       
          </div>
          {/* End Book Slider */}

          {/* Featured Bike */}
          <div className="freatured-product-wrapper">
            <div className="freatured-product-title">
              <h3 className="">Featured Bikes</h3>
              <Frc.Link className="" href="/list-oil-featured/featured/true/">See All <i className="material-icons">trending_flat</i></Frc.Link>
            </div>
            <div className="freatured-product-slider">
              <Frc.Swiper params={{ speed: 500, slidesPerView: 2.5, spaceBetween: 12 }}>
              {
                this.state.featured.length > 0 ?
                this.state.featured.map((featuredoil, index) =>
                <Frc.SwiperSlide key={index}>
                  <a href={`/OilDetails/product/`+featuredoil['id']+`/`}>
                  <Frc.Block className="no-margin no-padding">
                    <Frc.Block className="no-margin no-padding freatured-product-img">
                      {/* <img src='../static/bike/featured/01.png' alt="" title="" /> */}
                      <ReactImageFallback
                        src={featuredoil['photo']}
                        fallbackImage='./../static/placeholder_dark.svg'
                        initialImage='./../static/placeholder_dark.svg'
                        alt='image'
                        className=""
                      />
                    </Frc.Block>
                    <Frc.Block className="no-margin no-padding product-short-desc">
                      <span className="brand-name">{this.reduceStr(featuredoil['title'], 10)}</span>
                      <span className="brand-price">Tk. {parseInt(featuredoil['discount'] ? featuredoil['discount'] : featuredoil['price'])}</span>
                      {
                        parseInt(featuredoil['discount']) > 0 ?
                        <span className="brand-offer-price">OFF Tk. {parseInt(featuredoil['price'])-parseInt(featuredoil['discount'] ? featuredoil['discount'] : 0)}</span>
                        : 
                        <span className="brand-offer-price" style={{opacity: 0}}>0</span>
                      }
                      {/* <span className="brand-offer-price">OFF Tk. {featuredoil['discount']}</span> */}
                      <Frc.Link href={`/OilDetails/product/`+featuredoil['id']+`/`}>Order Now</Frc.Link>
                    </Frc.Block>
                  </Frc.Block>
                  </a>
                </Frc.SwiperSlide>
                )
                :
                ''
              }
              </Frc.Swiper>
            </div>
          </div>
          {/* End Featured Bike */}

        </Fragment>
      </Frc.Page>
    );
  }
}

// export default OilHome;
const mapDispachToProps = (dispach) => {
  return {
      changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
      changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(null, mapDispachToProps)(OilHome);