import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { AJAX_REQUEST, GET_STORAGE, USER } from "../../Constants/AppConstants";
import { RandomLoader } from '../Common/ReactContentLoader';
import ReactImageFallback from 'react-image-fallback';
let user_info = '';

class MyOrder extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "hide",
      orderDetail: [],
      orderId: this.$f7route.params.orderId,
    }
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    if (this.props.isAuthenticated) {
      user_info = GET_STORAGE(USER);
      this.getOrdersDetail();
    }
  }

  getOrdersDetail = () => {
    console.log("get orders")
    if (user_info) {
      AJAX_REQUEST('POST', 'shop/getOrderDetailsHistoryList', {
        customer_id: JSON.parse(user_info)['id'],
        order_id: this.state.orderId,
      }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            loading: false,
            orderDetail: results.response.data,
          })
        }
        else {
          this.setState({
            loading: false,
            orderDetail: []
          })
        }
      });
    }
  }

  last_page = () => {
    let last_link = '/MyOrder/';
    return last_link;
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function () {
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }


  render() {
    return (
      this.state.loading ?
        <Frc.Page name="" className="p-b-45vw myOrderPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
          <RandomLoader />
        </Frc.Page>
        :
        <Frc.Page name="" className="p-b-45vw myOrderPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
          <Frc.Navbar className="transparent" noShadow={true} noHairline={true} style={{ backgroundColor: '#292929' }}>
            <Frc.NavLeft backLink>
              {/* <Frc.Link href={this.last_page()} iconIos="f7:chevron_left" iconMd="material:arrow_back" /> */}
            </Frc.NavLeft>
            <Frc.NavTitle style={{ width: '100%', textAlign: 'left', color: '#929292' }}>
              Order #
              {
                ` ` + this.state.orderDetail['order_number'] ? this.state.orderDetail['order_number'] : '---'
              }
            </Frc.NavTitle>
            <Frc.NavRight>
            </Frc.NavRight>
          </Frc.Navbar>
          {/* <Frc.Block> */}
          <Frc.List className="orderDetails">
            <Frc.ListItem>
              <Frc.Row style={{ width: '100%' }}>
                <Frc.Col width="60">
                  <p>Date</p>
                  <p style={{ marginTop: '-15px' }}>{this.state.orderDetail['created_at']}</p>
                </Frc.Col>
                <Frc.Col width="40">
                  <p>Amount</p>
                  <p style={{ marginTop: '-15px' }}><span>৳ {this.state.orderDetail['totalAmount'] ? parseFloat(this.state.orderDetail['totalAmount'].toString().replace(',', '')).toFixed(2) : parseFloat('0'.toString().replace(',', '')).toFixed(2)}</span></p>
                </Frc.Col>
              </Frc.Row>
            </Frc.ListItem>
          </Frc.List>
          {/* <Frc.Block className="orderedProducts">
          {
            this.state.orderDetail['order_details'] ?
              this.state.orderDetail['order_details'].length > 0 ?
              this.state.orderDetail['order_details'].map((orders, index) => 
              <Frc.Link key={`prod`+index}>
                <Frc.Row className="orderedProductsList">
                    <Frc.Col className="orderedProductsListImage" width="25">
                    <ReactImageFallback
                          src={orders['item_img']}
                          fallbackImage='./../static/placeholder_dark.svg'
                          initialImage='./../static/placeholder_dark.svg'
                          alt='image'
                      />
                    </Frc.Col>
                    <Frc.Col className="orderedProductDetails" width="75">
                      <p className="title">{orders['title']}</p>
                      <p><span>TK. {orders['price'] ? parseFloat(orders['price'].toString().replace(',', '')).toFixed(2) : parseFloat('0'.toString().replace(',', '')).toFixed(2)}</span><span className="prodQuantity">X {orders['quantity'] }</span></p>
                      <p>Total: <span>৳ {parseFloat(orders['price'])* parseFloat(orders['quantity'])}</span></p>
                    </Frc.Col>
                </Frc.Row>                
              </Frc.Link>
              )
              :
              <p className="title">No Product Found!</p>
            :
            ''
          }
          </Frc.Block> */}
          <Frc.Block className="orderedProducts">
            <table style={{ borderSpacing: '0 5px' }}>
              <tbody>
                {
                  this.state.orderDetail['order_details'] ?
                    this.state.orderDetail['order_details'].length > 0 ?
                      this.state.orderDetail['order_details'].map((orders, index) =>
                        <tr key={`prod` + index} className="">
                          <td className="orderedProductsListImage" width="25%" style={{ display: 'table-cell' }}>
                            <ReactImageFallback
                              src={orders['item_img']}
                              fallbackImage='./../static/placeholder_dark.svg'
                              initialImage='./../static/placeholder_dark.svg'
                              alt='image'
                            />
                          </td>
                          <td className="orderedProductDetails" width="75%" style={{ padding: '2vw', borderBottomRightRadius: '6px', borderTopRightRadius: '6px', backgroundColor: '#292929' }}>
                            <p className="title">{orders['title']}</p>
                            <p><span>TK. {orders['price'] ? parseFloat(orders['price'].toString().replace(',', '')).toFixed(2) : parseFloat('0'.toString().replace(',', '')).toFixed(2)}</span><span className="prodQuantity">X {orders['quantity']}</span></p>
                            <p>Total: <span>৳ {parseFloat(orders['price']) * parseFloat(orders['quantity'])}</span></p>
                          </td>
                        </tr>
                      )
                      :
                      <p className="title">No Products Found!</p>
                    :
                    ''
                }
              </tbody>
            </table>
            <Frc.List className="orderDetails">
            <Frc.ListItem className="other-detail" style={{backgroundColor: 'unset'}}>
              <Frc.Row style={{ width: '100%' }}>
                <Frc.Col width="100">
                  <p><i class="fas fa-map-pin"></i> Delivery Address</p>
                  {
                    ((this.state.orderDetail['address'] == '') && (this.state.orderDetail['city'] == '') && (this.state.orderDetail['zipCode'] == '')) ?
                    <p style={{ marginTop: '-15px', fontWeight: '700' }}>
                      ---
                    </p>
                    :
                    <p style={{ marginTop: '-15px', fontWeight: '700' }}>{(this.state.orderDetail['address'] ? this.state.orderDetail['address']  : '') + (this.state.orderDetail['city'] ? ', '+this.state.orderDetail['city'] : '') + (this.state.orderDetail['zipCode'] ? ', '+this.state.orderDetail['zipCode'] : '')}</p>
                  }
                  
                </Frc.Col>
                <Frc.Col width="100">
                  <p><i class="fas fa-motorcycle"></i> Delivery Charge {this.state.orderDetail['shippingOptionText'] ? "("+this.state.orderDetail['shippingOptionText']+")"  : ''}</p>
                  <p style={{ marginTop: '-15px' }}><span>৳ {this.state.orderDetail['deliveryCharge'] ? parseFloat(this.state.orderDetail['deliveryCharge'].toString().replace(',', '')).toFixed(2) : parseFloat('0'.toString().replace(',', '')).toFixed(2)}</span></p>
                </Frc.Col>
              </Frc.Row>
            </Frc.ListItem>
          </Frc.List>
          </Frc.Block>
        </Frc.Page>
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(MyOrder);