class Apps extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showBottomNav: true,
      showHeaderNavbar: "true",
      isAuthenticated: false,

      active: '',

      user_info: [],

      // Framework7 Parameters
      f7params: {
        name: 'SpeedonX', // App name
        theme: 'auto', // Automatic theme detection
        view: {
          pushState: true,
          history: true,
          pushStateRoot: '',
          pushStateSeparator: '#',
          ignoreCache: true,
        },
        // App routes
        routes: routes,
        on:{
          pageBeforeIn:()=>{
            // this.props.changeFooterNavbarShow(false);
            this.props.changeHeaderNavbarShow('show');
            this.$f7.panel.close();
          }
        }
      }
    }
  }
  componentDidMount() {
    
    // console.log("url", this.$f7.view.current.router ? this.$f7.view.current.history[this.$f7.view.current.history.length-1] : '')
    {
      this.props.isAuthenticated ?
      this.setState({
        user_info: JSON.parse(GET_STORAGE(USER))
      })
      :
      ""
    }
    {
      (this.props.isAuthenticated && (this.$f7.view.current.history[this.$f7.view.current.history.length-1]!='/Cart/')) ?
      // this.showToastIcon()
      ''
      :
      ""
    }
    const url = new URL(window.location.href);
    const device_uuid = url.searchParams.get("device_uuid");
    const device_type = url.searchParams.get("device_type");

    if(device_uuid) {
      SET_STORAGE("LS_DEVICE_UID", device_uuid);
    }

    if(device_type) {
      SET_STORAGE("LS_DEVICE_TYPE", device_type);
    }

    setInterval(function(){
      this.checkTabActive();
    }.bind(this), 1000);
  }

  checkTabActive = () => {
    let current_link = this.$f7.view[0].router.currentRoute.url;
    // console.log(current_link)
    this.setState({
        active: current_link
    })
  }
  

  showToastIcon() {
    const self = this;
    // Create toast
    if (!self.toastIcon) {
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">checkmark_circle_fill</i>' : '<i class="material-icons">check_circle</i>',
        text: 'Successfully Logged In',
        position: 'center',
        closeTimeout: 2000,
      });
      // Open it
      self.toastIcon.open();
    }
    else {
      self.toastIcon.close();
      self.toastIcon.destroy();
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">checkmark_circle_fill</i>' : '<i class="material-icons">check_circle</i>',
        text: 'Successfully Logged In',
        position: 'center',
        closeTimeout: 2000,
      });
      self.toastIcon.open();
    }
  }

  // componentDidUpdate(prevProps, prevState) {
  //   if (this.state.showHeaderNavbar != prevState.showHeaderNavbar) {
  //     console.log("WHAT!",prevState.showHeaderNavbar)
  //   }
  // }

  // shouldComponentUpdate(nextProps, nextState) {
  //   if (nextProps.setting !== this.props.setting) {
  //     console.log("next props",nextProps.setting)
  //     // console.log("this props",this.props.setting)
  //     this.setState({
  //       showBottomNav: nextProps.setting.showHeaderNavbar,
  //       showHeaderNavbar: nextProps.setting.showFooterNavbar,
  //     })
  //     return true;
  //   }
  //   return false; 
  // }

  render() {
    return (
      <Fragment>
        <Navbar className="appNavBar" sliding={false} themeDark hidden={(this.props.setting.showHeaderNavbar == "hide") || this.props.setting.showHeaderNavbar == "false" ? true : false}>
          {
            // this.state.showHeaderNavbar == "back" ?
            this.props.setting.showHeaderNavbar == "back" ? // With Props
              // <HeaderNavSecondary />
              // <Suspense fallback={<img src='../static/icon-loader.gif'/>}>
                <HeaderNavSecondary />
              // </Suspense>
              :
              this.props.setting.showHeaderNavbar == "show" ?
                // <HeaderNavMain />
                // <Suspense fallback={<img src='../static/icon-loader.gif'/>}>
                  <HeaderNavMain />
                // </Suspense>
                :
                this.props.setting.showHeaderNavbar == "onlyback" ?
                  // <HeaderNavOnlyBack />
                  // <Suspense fallback={<img src='../static/icon-loader.gif'/>}>
                    <HeaderNavOnlyBack />
                  // </Suspense>
                  :
                  this.props.setting.showHeaderNavbar == "backsort" ?
                  // <HeaderNavOnlyBack />
                  // <Suspense fallback={<img src='../static/icon-loader.gif'/>}>
                    <HeaderNavBackwithSort />
                  // </Suspense>
                  :
                  this.props.setting.showHeaderNavbar == "onlybacklogin" ?
                  // <HeaderNavOnlyBack />
                  // <Suspense fallback={<img src='../static/icon-loader.gif'/>}>
                    <HeaderNavOnlyBackLogin />
                  // </Suspense>
                  :
                  ''
          }
        </Navbar>

        <App params={this.state.f7params} themeDark>

          {/* Left panel with cover effect*/}

          {/* <Suspense fallback={<img src='../static/icon-loader.gif'/>}> */}
            <LeftPanel />
          {/* </Suspense> */}

          
          {/* Your main view, should have "view-main" class */}
          <View main className="safe-areas" />
          {/* Popup */}

          {/* <Popup id="contact-popup">
              <Page>
                <Navbar>
                  <Link popupClose></Link>
                </Navbar>
                <Block className="hotline" popupOpen>
                  <Button className="call-btn"> Hotline </Button>
                  <p className="hotline-number">018 411 61 411</p>
                  <div className="hotline-number-call">
                    <Button popupClose> Cancel </Button>
                    <Button onClick={(e)=>this.callNumber(e, '+880 184 1161 411')}>Call</Button>
                  </div>
                </Block>
              </Page>
          </Popup> */}
          {/* <Button className="external" href="tel:+880 184 1161 411">Call</Button> */}

          
          <Fragment>
            {/* <FooterTabs /> */}
            <Suspense fallback={<img src='../static/icon-loader.gif'/>}>
              <FooterTabs />
            </Suspense>
            <Toolbar
              bottom
              className="footer-block"
              hidden={
                this.props.setting.showFooterNavbar ?
                  false
                  :
                  true
              }
            >
            <Suspense fallback={<img src='../static/icon-loader.gif'/>}>
              <FooterNav active={this.state.active}/>
            </Suspense>
              {/* <FooterNav /> */}
            </Toolbar>
          </Fragment>
        </App>
      </Fragment>
    )
  }
}



function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(Apps);