import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { AJAX_REQUEST, GET_STORAGE, USER, CURRENCY_FORMAT } from "./../../Constants/AppConstants";
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import ReactImageFallback from 'react-image-fallback';
import NoContent from '../Common/NoContent';
let user_info = '';

class MyOrder extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: true,
      showHeaderNav: "onlyback",
      orders: [],
    }
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    if(this.props.isAuthenticated){
      user_info = GET_STORAGE(USER);
      this.getOrders();
    }  
  }

  getOrders = () => {
    if (user_info) {
      AJAX_REQUEST('POST','shop/getOrderHistoryList',{
        customer_id: JSON.parse(user_info)['id'],
        offset: 0,
      }).then(results => {
          if (parseInt(results.response.code) === 1000) {
            this.setState({
              loading: false,
              orders: results.response.data
            })
          }
          else {
            this.setState({
              loading: false,
              orders: []
            })
          }
        });
    }
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  render() {
    return (
      this.state.loading ?
      <Frc.Page name="" className="p-b-45vw myOrderPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Frc.Page name="" className="p-b-45vw myOrderPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <Fragment>
          <div className="my-order-list">
            <Frc.List className="no-padding no-margin">
              {
                this.state.orders ?
                this.state.orders.length > 0 ?
                  this.state.orders.map((order, index) => 
                    <Frc.ListItem key={`order`+index} link={`/OrderDetail/order/`+order['order_id']+`/`}>
                      <i className="fas fa-shopping-cart"></i>
                      <div className="order-id-date">
                        <span>Order # {order['order_number']}</span>
                        <span>{order['created_at']}</span>
                      </div>
                      <div className="order-pirce">
                        ৳ <span>{order['totalAmount'] ? parseFloat(order['totalAmount'].toString().replace(',', '')).toFixed(2) : parseFloat('0'.toString().replace(',', '')).toFixed(2)}</span>
                      </div>
                    </Frc.ListItem>
                  )
                  :
                  // <Frc.ListItem link="#">
                  //     <div className="order-id-date">
                  //       <span>You haven't ordered yet!</span>
                  //     </div>
                  //   </Frc.ListItem>
                  <div className="notification-details" style={{width: '100%'}}>
                      <NoContent icon = 'fa fa-shopping-cart' text="No Orders Found!" />
                  </div>
                  :
                  ''
              }
            </Frc.List>
          </div>
        </Fragment>
      </Frc.Page>
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(MyOrder);