import React, { Component } from 'react';
import * as Frc from 'framework7-react';
import 'swiper/css/swiper.css';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from './../../Store/actions/actionTypes';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, AJAX_REQUEST, GET_STORAGE, USER } from "../../Constants/AppConstants";
import Modal from 'react-responsive-modal';
let user_info = '';

class EditProfile extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showBottomNav: false,
      showHeaderNav: "hide",

      name: '',
      phone: '',
      email: '',
      address: '',
      city: '',
      zipCode: '',

      open: false,
      message: 'Something went wrong',
      status: 'error',
    }
  }

componentDidMount() {
  AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
    page_name: "profile_edit",
    content_id: 0,
    module: "user",
  }).then(results => {            
  });

  this.props.changeFooterNavbarShow(this.state.showBottomNav);
  this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
  if(this.props.isAuthenticated){
    user_info = GET_STORAGE(USER);
    this.getUserInfo();
    this.getUserAddress();
  }    
}

getUserInfo = () => {
  if (user_info) {
    AJAX_REQUEST('POST','user/getCustomerProfile',{
      customer_id: JSON.parse(user_info)['id']
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
              name : results.response.data.name,
              phone : results.response.data.mobile_number,
              email : results.response.data.email ? results.response.data.email : '',
          })
        }
      });
  }
} 

getUserAddress = () => {
  AJAX_PUBLIC_REQUEST('POST','user/getUserAddress',{
    customer_id: JSON.parse(user_info)['id']
  }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
            address: results.response.data.address ? results.response.data.address : '',
            city: results.response.data.city ? results.response.data.city : '',
            zipCode: results.response.data.zip_code ? results.response.data.zip_code : '',
        })
      } else {
      }
    });
}

componentDidUpdate(prevProps, prevState) {
  if (this.props.sort != prevProps.sort) {
      this.setState({
        name: '',
        phone: '',
        email: '',
        address: '',
        city: '',
        zipCode: '',
  
      })
      this.getUserInfo();
      this.getUserAddress();
  }
}

handleChange = event => {
  const { name, value } = event.target
  this.setState({
    [name]: value
  },()=> {
    // console.log(name, value)
  })
}

handleSubmit = () => {
  let data = {
    action_type: "update",
    name:this.state.name,
    email:this.state.email,
    address: this.state.address,
    city: this.state.city,
    zip_code: this.state.zipCode
  };

  AJAX_REQUEST("POST", "user/updateCustomerProfile", data).then(results => {
    if (parseInt(results.response.code) === 1000) {
          this.setState({
            open: true,
            status: results.response.status,
            message: results.response.message
          })
    } else {
      // alert(results.response.message);
      this.showToastIcon(results.response.message)
    }
  });
}

onOpenModal = () => {
  this.setState({ open: true });
};

onCloseModal = () => {
  this.setState({ open: false });
};

showToastIcon(text) {
  const self = this;
  // Create toast
  if (!self.toastIcon) {
    self.toastIcon = self.$f7.toast.create({
      icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
      text: text,
      position: 'center',
      closeTimeout: 3000,
    });
    self.toastIcon.open();
  }
  else {
    self.toastIcon.close();
    self.toastIcon.destroy();
    self.toastIcon = self.$f7.toast.create({
      icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
      text: text,
      position: 'center',
      closeTimeout: 3000,
    });
    self.toastIcon.open();
  }
}

onPageAfterIn = () => {
  //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
  // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
  setTimeout(function(){
    console.log("Action")
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);

    if(this.props.isAuthenticated){
      this.getNotifications();
    }
    else {
      console.log(this);
      this.$f7.view[0].router.navigate('/');
    }
  }.bind(this), 10);
}

  render() {
    const { open } = this.state;
    return (
      <Frc.Page name="edit-profile" className="user-profile-page" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        {/* Modal */}
        <Modal modalId="signupPopup" closeIconId="closeSignupPop" open={open} onClose={this.onCloseModal} center>
          {
            this.state.status == "success" ?
            <Frc.Link className="successSignup" iconIos="f7:checkmark_alt_circle" iconMd="material:check_circle_outline" href="#" />
            :
            <Frc.Link className="errorSignup" iconIos="f7:info_circle_fill" iconMd="material:error" href="#" />
          }
          
          <h2>{this.state.message}</h2>
          <Frc.Link className="fw-login" href="/user-profile/" onClick={()=>this.onCloseModal()} animate={false} reloadCurrent={true}>Ok</Frc.Link>
        </Modal>
        <Frc.Navbar className="transparent user-profile-nav user-profile-nav-edit">
          <Frc.NavLeft backLink>
            {/* <Frc.Link href='/user-profile/' iconIos="f7:chevron_left" iconMd="material:arrow_back" text="Back"/> */}
          </Frc.NavLeft>
          <Frc.NavTitle></Frc.NavTitle>
          <Frc.NavRight>
          </Frc.NavRight>
        </Frc.Navbar>
        {/* Slider */}

        <Frc.Block className="user-profile-wrapper">
          <Frc.Block className="user-profile-container">
            <div className="user-profile-info">
              <ul>
                <li>
                  <span><i className="fas fa-user"></i></span>
                    Name
                    <Frc.Input
                      className="text-custom"
                      type="text"
                      placeholder="Name"
                      onChange={this.handleChange}
                      name="name"
                      value={this.state.name ? this.state.name : ''}
                    >
                    </Frc.Input>
                </li>
                <li>
                  <span><i className="fas fa-phone-volume"></i></span>
                    Phone
                    <Frc.Input
                      className="text-custom"
                      type="text"
                      placeholder="Phone Number"
                      name="phone"
                      value={this.state.phone ? this.state.phone : ''}
                      readonly={true}
                    ></Frc.Input>
                </li>
                <li>
                  <span><i className="fas fa-home-lg-alt"></i></span>
                    Address
                    <Frc.Input
                      className="text-custom"
                      type="text"
                      placeholder="Address"
                      onChange={this.handleChange}
                      name="address"
                      value={this.state.address ? this.state.address : ''}
                    >
                    </Frc.Input>
                    <Frc.Input
                      className="text-custom"
                      type="text"
                      placeholder="City"
                      onChange={this.handleChange}
                      name="city"
                      value={this.state.city ? this.state.city : ''}
                    >
                    </Frc.Input>
                    <Frc.Input
                      className="text-custom"
                      type="number"
                      placeholder="Postal/Zip Code"
                      onChange={this.handleChange}
                      name="zipCode"
                      value={this.state.zipCode ? this.state.zipCode : ''}
                    >
                    </Frc.Input>
                    <p>
                      <strong>{this.state.address ? this.state.address : '' }{  this.state.city ? ', '+this.state.city : '' }{  this.state.zipCode ? ', '+this.state.zipCode : '' }</strong>
                    </p>
                </li>
                <li>
                  <span><i className="fas fa-envelope"></i></span>
                    E-mail
                    <Frc.Input
                      className="text-custom"
                      type="text"
                      placeholder="Email Address (Optional)"
                      onChange={this.handleChange}
                      name="email"
                      value={this.state.email ? this.state.email : ''}
                    >
                    </Frc.Input>
                      {/* <strong>{this.state.form ? this.state.form.email ? this.state.form.email : '---' : '--'}</strong> */}
                </li>
              </ul>
            </div>
            <Frc.Button type="button" style={{position: 'relative'}} onClick={this.handleSubmit} className="btn-custom">Submit</Frc.Button>

          </Frc.Block>
        </Frc.Block>
      </Frc.Page>
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(EditProfile);