import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import 'swiper/css/swiper.css';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from './../../Store/actions/actionTypes';
import { GET_STORAGE, USER, AJAX_REQUEST } from './../../Constants/AppConstants';
import NoContent from '../Common/NoContent';
import { RandomLoader } from '../Common/ReactContentLoader';
let user_info = '';

class Notifications extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showBottomNav: true,
      showHeaderNav: "show",
      notifications: [],
      loading: true,
    }
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    
    if(this.props.isAuthenticated){
      user_info = GET_STORAGE(USER);
      this.getNotifications()
    }   
    else {
      this.setState({
        notifications: []
      })
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.isAuthenticated != prevProps.isAuthenticated) {
      user_info = GET_STORAGE(USER);
        this.setState({
            notifications: [],
        })
        this.props.isAuthenticated ?
        this.getNotifications()
        :
        ''
    }
}

  checkNotification = () => {
    {
      this.props.isAuthenticated ?
      ""
      :
      this.showToastIcon("Please Login First!")
    }
  }

  getNotifications = () => {
    AJAX_REQUEST('POST','setting/getNotificatonsList',{
      customer_id: JSON.parse(user_info)['id']
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          this.setState({
            notifications: results.response.data,
            loading: false
          })
        }
        else {
          this.setState({
            loading: false
          })
        }
      });
  }

  reduceStr = (str, size) => {
    if (str.length > 0) {
        if (str.length > size) {
            str = str.substring(0, size) + '..';
        }
    }
    else {
        str = '---';
    }
    return str;

}

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
      if(this.props.isAuthenticated){
        this.getNotifications();
      }
      else {
        console.log(this);
        this.$f7.view[0].router.navigate('/');
      }
    }.bind(this), 10);
  }

  render() {
    return (
      this.state.loading ?
      <Frc.Page onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Frc.Page name="user-profile" className="p-b-45vw" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <Fragment>
          <div className="notification-wrapper">
            <h3 className="inner-title">NOTIFICATIONS</h3>
            
            {
              this.state.notifications ?
              this.state.notifications.length > 0 ?
              <ul className="notification-list">
                {
                  this.state.notifications.map((notification, index) =>
                  <a key={`noti`+index}  className="link" href={`/notifications/notification/`+notification['push_id']+`/`} style={{width: '100%'}}>
                    {/* <li style={{width: '100%'}} className={index % 2 == 0 ? 'active' : ''}> */}
                    <li style={{width: '100%'}} className={notification['tap_status'] != 0 ? 'active' : ''}>
                        <div className="notification-alert" style={{width: '20%'}}></div>
                        <div className="notification-details" style={{width: '80%'}}>
                          <h3>{notification['push_title']}</h3>
                          <p>
                            {this.reduceStr(notification['push_details'], 100)}
                          </p>
                        </div>
                    </li>
                  </a>
                  )
                }
              </ul>
              :
                  <div className="notification-details" style={{width: '100%'}}>
                    {/* <p style={{textAlign: 'center'}}>
                      No Notifications Found!
                    </p> */}
                    <NoContent icon = 'fa fa-bell' text="No Notifications Found!" />
                  </div>
              :
              ''
            }
              {/* <li className="active">
                <div className="notification-alert"></div>
                <div className="notification-details">
                  <h3>This is the title of this</h3>
                  <p>
                    The body text for this notification. The icon in the lefttelling is that this notification is still unread.
                  </p>
                </div>
              </li>
              <li>
                <div className="notification-alert"></div>
                <div className="notification-details">
                  <h3>This is the title of this</h3>
                  <p>
                    The body text for this notification. The icon in the lefttelling is that this notification is still unread.
                  </p>
                </div>
              </li>
              <li className="active">
                <div className="notification-alert"></div>
                <div className="notification-details">
                  <h3>This is the title of this</h3>
                  <p>
                    The body text for this notification. The icon in the lefttelling is that this notification is still unread.
                  </p>
                </div>
              </li> */}
          </div>
        </Fragment>
      </Frc.Page>
    );
  }
}

function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(Notifications);