import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import { SITE_TITLE, AJAX_REQUEST } from "./../../Constants/AppConstants";


class TipsDetails extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "onlyback",

      notId: this.$f7route.params.notId,

      notification: [],

      errorMessage: '',

      search: '',
      itemFound: false,
      searchResult: [],

      video: ''
    }
    document.title = `Notification Details - ${SITE_TITLE}`;
  }


    componentDidMount() {
        this.props.changeFooterNavbarShow(this.state.showBottomNav);
        this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
        this.getNotificationDetails();
    }

    showToastIcon() {
        const self = this;
        // Create toast
        if (!self.toastIcon) {
        self.toastIcon = self.$f7.toast.create({
            icon: self.$theme.ios || self.$theme.aurora ? '<a href="/login/"><i class="f7-icons">info_circle_fill</i></a>' : '<a href="/login/"><i class="material-icons">info</i></a>',
            text: 'Please Log In First',
            position: 'center',
            closeTimeout: 2000,
        });
        }
        // Open it
        self.toastIcon.open();
    }

    getNotificationDetails = () => {
        AJAX_REQUEST("POST", "setting/getNotificatonDetails", {
            notificaton_id: this.state.notId,
        }).then(results => {
        if (parseInt(results.response.code) === 1000) {
            this.setState({
            loading: false,
            notification: results.response.data,
            });
        } else {
            this.setState({
            loading: false,
            errorMessage: results.response.message,
            })
        }
        });
    }

    onPageAfterIn = () => {
      //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
      // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
      setTimeout(function(){
        console.log("Action")
        this.props.changeFooterNavbarShow(this.state.showBottomNav);
        this.props.changeHeaderNavbarShow(this.state.showHeaderNav);

        if(this.props.isAuthenticated){
          this.getNotificationDetails();
        }
        else {
          console.log(this);
          this.$f7.view[0].router.navigate('/');
        }
      }.bind(this), 10);
    }

  render() {    
    return (
      this.state.loading ?
      <Frc.Page name="detailsbike" className="product-details-content"  onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Fragment>
        <Frc.Page name="detailsbike" className="product-details-content"  onPageAfterIn={this.onPageAfterIn} noSwipeback>
          <Frc.Block className="product-details-wrapper" style={{paddingBottom: '22vw'}}>
            {/* Product details Overview */}
            {
              this.state.notification ?
              <Frc.Block className="product-details-overview">
                <div className="data-table">
                    <span dangerouslySetInnerHTML={{ __html: (this.state.notification.push_details ? this.state.notification.push_details : '') }}></span>
                    <p>
                        <i className="fa fa-clock"></i>
                        {this.state.notification.delivery_date_time ? ` `+this.state.notification.delivery_date_time : '' }
                    </p>
                    {
                      ((this.state.notification.push_target_url != '')) ?
                      <a href={this.state.notification.push_target_url} class="button button-round button-fill link ripple" style={{backgroundColor: '#ed2f42'}}>Read More</a>
                      :
                      ''
                    }
                    
                </div>
              </Frc.Block>
              : ''
            }
          </Frc.Block>

        </Frc.Page>
      </Fragment>
    );
  }
}

// export default BikeHome;

function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(TipsDetails);