import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { AJAX_PUBLIC_REQUEST, SET_STORAGE } from '../../Constants/AppConstants';
// import history from '../../history';
import Modal from 'react-responsive-modal';

class SignUp extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showBottomNav: false,
      showHeaderNav: "hide",
      currentStep: 1,
      email: '',
      name: '',
      phone: '',
      password: '',
      retype_password: '',
      open: false,
      message: 'Something went wrong',
      status: 'error',
    }
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };
 
  onCloseModal = () => {
    this.setState({ open: false });
  };

  handleChange = event => {
    // console.log(event);
    const { name, value } = event.target
    this.setState({
      [name]: value
    },()=> {
      // console.log(this.state);
    })
  }

  handleSubmit = event => {
    event.preventDefault();
    if(this.state.password =='' || this.state.retype_password=='') {
      // alert('Password is Empty');
      this.showToastWithButton("Password Field is Empty");
      return;
    }
    else if(this.state.password !== this.state.retype_password) {
      // alert('password is not match with confirm password');
      this.showToastWithButton("Passwords do not match!");
      return;
    }
    else if(this.state.password.length <= 3 ) {
      // alert('password is not match with confirm password');
      this.showToastWithButton("Password Should be at least 4 character!");
      return;
    }

    let data = {
      name:this.state.name,
      mobile_number:this.state.phone,
      email:this.state.email,
      password:this.state.password,
      confirm_password:this.state.retype_password,
    };

    AJAX_PUBLIC_REQUEST("POST", "user/customerRegister", data).then(results => {
      if (parseInt(results.response.code) === 1000) {
            this.setState({
              open: true,
              status: results.response.status,
              message: results.response.message
            })
            SET_STORAGE("SignUpInfo", JSON.stringify(data))
      } else {
        // alert(results.response.message);
        this.showToastIcon(results.response.message)
      }
    });
  }

  _next = () => {
    let currentStep = this.state.currentStep;
    
    if(this.state.name=='' || this.state.phone=='') {
        // alert('Name or Mobile Number is empty');
        this.showToastWithButton("Name or Mobile Number is empty");
        return;
    }
    else if(this.state.phone.length <= 10 || this.state.phone.length > 11) {
      this.showToastWithButton("Phone Number is not Valid");
      return;
    }
    else if(this.state.phone.charAt(0) != 0 || this.state.phone.charAt(1) != 1) {
      this.showToastWithButton("Phone Number is not Valid");
      return;
    }
    
    currentStep = currentStep >= 2 ? 3 : currentStep + 1
    this.setState({
      // currentStep: currentStep
      currentStep: 2
    })
  }

  _prev = () => {
    let currentStep = this.state.currentStep
    currentStep = currentStep <= 1 ? 1 : currentStep - 1
    this.setState({
      // currentStep: currentStep
      currentStep: 1
    })
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
      page_name: "register_user",
      content_id: 0,
      module: "user",
    }).then(results => {            
    });
  }

  previousButton() {
    let currentStep = this.state.currentStep;
    if (currentStep !== 1) {
      return (
        <button
          className="insurance-btn-back"
          type="button" onClick={this._prev}>
          <Frc.Icon icon="icon-back"></Frc.Icon>
        </button>
      )
    }
    return null;
  }

  nextButton() {
    let currentStep = this.state.currentStep;
    if (currentStep < 2) {
      return (
        <button
          className="auth-sing-btn auth-sing-up-btn button"
          type="button" onClick={this._next}>
          <i className='far fa-arrow-right'></i>
        </button>
      )
    }
    return null;
  }

  showToastWithButton(text) {
    const self = this;
    // Create toast
    if (!self.toastWithButton) {
      self.toastWithButton = self.$f7.toast.create({
        text: text,
        closeButton: true,
        closeTimeout: 2000,
      });
      self.toastWithButton.open();
    }
    else {
      self.toastWithButton.close();
      self.toastWithButton.destroy();
      self.toastWithButton = self.$f7.toast.create({
        text: text,
        closeButton: true,
        closeTimeout: 2000,
      });
      self.toastWithButton.open();
    }
    
  }

  showToastIcon(text) {
    const self = this;
    // Create toast
    if (!self.toastIcon) {
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 3000,
      });
      self.toastIcon.open();
    }
    else {
      self.toastIcon.close();
      self.toastIcon.destroy();
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<i class="f7-icons">info_circle_fill</i>' : '<i class="material-icons">info</i>',
        text: text,
        position: 'center',
        closeTimeout: 3000,
      });
      self.toastIcon.open();
    }
  }

  onPageAfterIn = () => {
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
  
      if(this.props.isAuthenticated){
        this.$f7.view[0].router.navigate('/');
      }
      else {
        // this.$f7.view[0].router.navigate('/');
      }
    }.bind(this), 10);
  }

  render() {
    const { open } = this.state;
    return (
      <Fragment>
      <Frc.Page name="user-profile" className="signUpPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>

        {/* Modal */}
        <Modal closeOnOverlayClick={false} modalId="signupPopup" closeIconId="closeSignupPop" open={open} onClose={this.onCloseModal} center>
          {
            this.state.status == "success" ?
            <Frc.Link className="successSignup" iconIos="f7:checkmark_alt_circle" iconMd="material:check_circle_outline" href="#" />
            :
            <Frc.Link className="errorSignup" iconIos="f7:info_circle_fill" iconMd="material:error" href="#" />
          }
          
          <h2>{this.state.message}</h2>
          <Frc.Link className="fw-login" href="/loginfromsignup/" onClick={()=>this.onCloseModal()} animate={false}>Login Now</Frc.Link>
        </Modal>

        <Frc.Navbar className="transparent signUpPage-page-nav">
          <Frc.NavLeft>
            {this.state.currentStep >= 2 ?
              this.previousButton()
              : 
              // ''
              <Frc.Link back iconIos="f7:chevron_left" iconMd="material:arrow_back" />
            }
          </Frc.NavLeft>
          <Frc.NavTitle></Frc.NavTitle>
        </Frc.Navbar>
        <div>
          <form onSubmit={this.handleSubmit}>
            {/* 
        render the form steps and pass required props in
      */}
            <Step1
              currentStep={this.state.currentStep}
              handleChange={this.handleChange}
              name={this.state.name}
              phone={this.state.phone}
              email={this.state.email}
            />
            <Step2
              currentStep={this.state.currentStep}
              handleChange={this.handleChange}
              handleSubmit={this.handleSubmit}
              password={this.state.password}
              retype_password={this.state.retype_password}
            />
            {/* <Step3 
          currentStep={this.state.currentStep} 
          handleChange={this.handleChange}
          password={this.state.password}
        /> */}
            {/* {this.previousButton()} */}
            {this.nextButton()}

          </form>
        </div>

        </Frc.Page>
          
      </Fragment>
    );
  }
}

function Step1(props) {
  if (props.currentStep !== 1) {
    return null
  }
  return (
    <div className="login-wrapper">
      <div className="sign-in-logo">
        <img src='../static/speedonx-logo.svg' />
      </div>
      <h3 className="login-wrapper-title">Sign Up</h3>
      <div className="auth-sing-info">
        <Frc.List>
          <Frc.ListInput
            type="text"
            placeholder="Name"
            name="name"
            value={props.name}
            onChange={props.handleChange}
          ></Frc.ListInput>
          <Frc.ListInput
            type="number"
            placeholder="Mobile Number"
            value={props.phone}
            name="phone"
            onChange={props.handleChange}
          ></Frc.ListInput>
          <Frc.ListInput
            type="text"
            placeholder="Email (Optional)"
            name="email"
            value={props.email}
            onChange={props.handleChange}
          ></Frc.ListInput>
          <p className="form-note">By signing up, you agreed to our Terms &amp; Conditions and Privacy Policy</p>
          <div className="auth-sing-up-now">
            <span>Already Registered?</span>
            <Frc.Link href="/login/">	Login Now</Frc.Link>
          </div>
        </Frc.List>
      </div>
    </div >
  );
}

function Step2(props) {
  if (props.currentStep !== 2) {
    return null
  }
  return (
    <React.Fragment>
      <div className="login-wrapper" style={{backgroundPositionY: '20%'}}>
        <div className="sign-in-logo">
          <img src='../static/speedonx-logo.svg' />
        </div>
        <h3 className="login-wrapper-title">Set Password</h3>
        <div className="auth-sing-info">
          <Frc.List>
            <Frc.ListInput
              type="password"
              placeholder="Password"
              name="password"
              id="password"
              value={props.password}
              onChange={props.handleChange}
            ></Frc.ListInput>
            <Frc.ListInput
              type="password"
              placeholder="Retype Password"
              name="retype_password"
              onChange={props.handleChange}
            ></Frc.ListInput>

            <Frc.Button style={{marginTop: '25px'}} type="submit" className="auth-sing-btn auth-sing-up-btn button" > <i className='far fa-arrow-right'></i></Frc.Button>
          </Frc.List>
        </div>
      </div>
    </React.Fragment>
  );
}

