import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import { AJAX_PUBLIC_REQUEST, ITEM_COUNT, SET_STORAGE, GET_STORAGE } from '../../Constants/AppConstants';
import ReactImageFallback from 'react-image-fallback';
class HeaderNavSecondary extends Component {
    constructor(props) {
        super(props)
        this.state = {
            search: '',
            itemFound: false,
            searchResult: [],
        }
    }

    clearSearch = () => {
        this.setState({
            itemFound: false
        },()=> {
            this.processSearch();
          })
    }

    searchAll = (e) => {
        const { value } = e.target;
        if(value.length > 3){
            this.setState({
                itemFound: true
            },()=> {
                this.processSearch(value);
              })
        }
        else {
            this.setState({
                itemFound: false
            },()=> {
                this.processSearch(value);
              })
        }
        
    }

    processSearch = (value='') => {
        this.state.itemFound ?
        AJAX_PUBLIC_REQUEST("POST", "product/getTagwiseProducts",{
            tag: value,
            offset: 0,
        }).then(results => {
            if (parseInt(results.response.code) === 1000) {
                  this.setState({
                    searchResult: results.response.data
                  })
                  this.searchPopover.open()
            } else {
                this.setState({
                    searchResult: []
                })
                this.searchPopover.open()
            }
          })
        :
        this.searchPopover.close()
    }

    last_page = () => {
        let current_link = this.$f7.view[0].router.currentRoute.url ? this.$f7.view[0].router.currentRoute.url : '/home/';
        let last_link = this.$f7.view[0].router.previousRoute.url ? this.$f7.view[0].router.previousRoute.url : '/home/';
        if(current_link.includes("list-bike") && !last_link.includes("category")){
            last_link = "/bikehome/";
        }
        else if (current_link.includes("list-oil") && !last_link.includes("category")){
            last_link = "/oilhome/";
        } 
        else if (current_link.includes("list-bike") && last_link.includes("category")){
            last_link = "/category/";
        } 
        else if (current_link.includes("list-oil") && last_link.includes("category")){
            last_link = "/category/";
        } 
        else if (current_link.includes("Checkout")){
            last_link = "/home/";
        }
        else if (current_link.includes("Cart")){
            last_link = "/oilhome/";
        }
        else if (current_link.includes("OrderDeatils")){
            last_link = "/MyOrder/";
        }
        return last_link;
    }

    // last_page = () => {
    //     let history = JSON.parse(GET_STORAGE('f7router-view_main-history'));
    //     SET_STORAGE('f7router-view_main-history', JSON.stringify(history.splice(-1,1)));
    //     return history[history.length - 2];
    // }


    goToProduct = (e, type, id) => {
        e.preventDefault();
        let self = this;
        setTimeout(function(){ 
            {
                    type == 1 ? 
                    self.$f7.view[0].router.navigate('/BikeDetails/product/'+id+'/')                
                    :
                    self.$f7.view[0].router.navigate('/OilDetails/product/'+id+'/')       
            }
         }, 1000);
        
    }

    render() {
        return (
            <Fragment>
                <Frc.NavLeft backLink>
                    {/* <Frc.Link href={this.last_page()} iconIos="f7:chevron_left" iconMd="material:arrow_back" /> */}
                </Frc.NavLeft>
                <Frc.NavTitle className="siteLogo" sliding>
                    {
                        (ITEM_COUNT() > 0 && (this.$f7.view[0].router.currentRoute.url != '/Cart/')) ?
                        <img src='../static/speedonx-logo.svg' style={{width: '45%'}}/>
                        :
                        <img src='../static/speedonx-logo.svg' style={{width: '35%'}}/>
                    }
                    {/* <img src='../static/logo.png' /> */}
                </Frc.NavTitle>
                <Frc.NavRight>
                    <Frc.Link searchbarEnable=".searchbar-demo" iconIos="f7:search" iconAurora="f7:search" iconMd="material:search"></Frc.Link>
                    {
                        (ITEM_COUNT() > 0 && (this.$f7.view[0].router.currentRoute.url != '/Cart/')) ?
                        <Frc.Link href="/Cart/" iconIos="f7:cart" iconAurora="f7:cart" iconMd="material:shopping_cart"><span className="cart-counter">{ITEM_COUNT()}</span></Frc.Link>
                        : ''
                    }
                </Frc.NavRight>

                {/* Search Input */}
                <Frc.Searchbar
                    className="searchbar-demo"
                    expandable
                    // searchContainer=".search-list"
                    searchIn=".item-title"
                    name="search"
                    onChange={this.searchAll}
                    onClickClear={this.clearSearch}
                    onClickDisable = {this.clearSearch}
                >
                </Frc.Searchbar>



                {/* Search Result */}
                <Frc.Popover className="popover-menu popover-on-bottom" ref={popover => this.searchPopover = popover} style={{width: '100%', zIndex: '99999'}} closeByOutsideClick={false} closeByBackdropClick={false} >
                {
                        this.state.searchResult ?
                        this.state.searchResult.length > 0 ?
                        <Frc.List mediaList>
                        {
                            this.state.searchResult.map((searchRes, index) =>                         
                                // <Frc.ListItem link="#" popoverClose title="Dialog" /> 
                                <Frc.ListItem
                                    popoverClose
                                    onClick = {(e)=>this.goToProduct(e, searchRes['category_id'], searchRes['id'])}
                                    className = 'searchresult'
                                    key={`search`+index}
                                    // link={
                                    //         searchRes['category_id'] == 1 ? 
                                    //         '/BikeDetails/product/'+searchRes['id']+'/'
                                    //         :
                                    //         '/OilDetails/product/'+searchRes['id']+'/'
                                    // }
                                    link = "#"
                                    view=".view-main"
                                    title={searchRes['title']}
                                    // after={`৳`+searchRes['price']}
                                    after={parseInt(searchRes['discount']) > 0 ? `৳`+searchRes['discount'] : `৳`+searchRes['price']}
                                    subtitle={searchRes['tag']}
                                    text={searchRes['category_id'] == 1 ? 'Bike' : 'Oil'}
                                >
                                    {
                                    searchRes['category_id'] == 1 ? 
                                    <ReactImageFallback
                                        src={searchRes['photo']}
                                        fallbackImage='./../static/placeholder_dark.png'
                                        initialImage='./../static/placeholder_dark.png'
                                        alt='image'
                                        width='80'
                                        style={{maxWidth: '80px'}}
                                        slot="media"
                                    />
                                    
                                    : 
                                    <ReactImageFallback
                                        src={searchRes['photo']}
                                        fallbackImage='./../static/placeholder_dark.png'
                                        initialImage='./../static/placeholder_dark.png'
                                        alt='image'
                                        width='80'
                                        style={{maxWidth: '80px'}}
                                        slot="media"
                                    />
                                    }
                                </Frc.ListItem>                       
                            )
                        }
                        </Frc.List>
                        : 
                        <Frc.List style={{textAlign: 'center'}}>
                            <Frc.ListItem className="no-result" link="#" popoverClose title="No Result Found!" />
                        </Frc.List>
                        :
                        ''
                    }
                </Frc.Popover>
            </Fragment>
        );
    }
}

export default HeaderNavSecondary;