import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';
import ReactImageFallback from 'react-image-fallback';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, ITEM_COUNT } from "./../../Constants/AppConstants";


class TipsDetails extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: true,
      showHeaderNav: "onlyback",

      ttId: this.$f7route.params.ttId,

      tipsData: [],

      errorMessage: '',

      search: '',
      itemFound: false,
      searchResult: [],

      video: ''
    }
    document.title = `Tips & Tricks Details - ${SITE_TITLE}`;
  }


    componentDidMount() {
        this.props.changeFooterNavbarShow(this.state.showBottomNav);
        this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
        this.getTipDetails();
    }

    showToastIcon() {
        const self = this;
        // Create toast
        if (!self.toastIcon) {
        self.toastIcon = self.$f7.toast.create({
            icon: self.$theme.ios || self.$theme.aurora ? '<a href="/login/"><i class="f7-icons">info_circle_fill</i></a>' : '<a href="/login/"><i class="material-icons">info</i></a>',
            text: 'Please Log In First',
            position: 'center',
            closeTimeout: 2000,
        });
        }
        // Open it
        self.toastIcon.open();
    }

    getTipDetails = () => {
        AJAX_PUBLIC_REQUEST("POST", "home/tipsAndTricksDetails", {
            tips_and_tricks_id: this.state.ttId,
        }).then(results => {
        if (parseInt(results.response.code) === 1000) {
            this.setState({
            loading: false,
            tipsData: results.response.data,
            photos: results.response.data.photo,
            video: results.response.video_url,
            });
        } else {
            this.setState({
            loading: false,
            errorMessage: results.response.message,
            })
        }
        });
    }

    onPageAfterIn = () => {
      //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
      // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
      setTimeout(function(){
        console.log("Action")
        this.props.changeFooterNavbarShow(this.state.showBottomNav);
        this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
      }.bind(this), 10);
    }

    onPtrRefresh = (done) => {
      setTimeout(() => {
        this.getTipDetails();
        done();
      }, 1000);
    }

  render() {    
    return (
      this.state.loading ?
      <Frc.Page name="detailsbike" className="product-details-content"  onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Fragment>
        <Frc.Page name="detailsbike" className="product-details-content"  onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
          <Frc.Block className="product-details-wrapper" style={{paddingBottom: '22vw'}}>
          
            <div className="product-details-container">
              
                      <div className="product-details">
                        {/* <img src={image['image']}/> */}
                        <ReactImageFallback
                          src={this.state.tipsData['image']}
                          fallbackImage='./../static/placeholder_dark_big.svg'
                          initialImage='./../static/placeholder_dark_big.svg'
                          alt='image'
                          className="swiper-lazy"
                        />
                      </div>
                      {
                          this.state.video ?
                          <iframe style={{width: '100vw'}} src={this.state.video} frameborder="0" allowFullScreen></iframe>
                          : 
                          ''
                      }
                      
            </div>



            <Frc.Block className="product-details-price">
              <div className="brand-name">{this.state.tipsData ? this.state.tipsData.title : '---'}</div>
              <div className="brand-engine-capacity">{this.state.tipsData ? this.state.tipsData.publish_date ? this.state.tipsData.publish_date : '---' : '---'}</div>
            </Frc.Block>

            {/* Product details Overview */}
            {
              this.state.tipsData ?
              <Frc.Block className="product-details-overview">
                <div className="data-table">
                    <span dangerouslySetInnerHTML={{ __html: (this.state.tipsData.details ? this.state.tipsData.details : '') }}></span>
                </div>
              </Frc.Block>
              : ''
            }
          </Frc.Block>

        </Frc.Page>
      </Fragment>
    );
  }
}

// export default BikeHome;

function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(TipsDetails);