import React, { Component } from 'react';
import * as Frc from 'framework7-react';
import 'swiper/css/swiper.css';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from './../../Store/actions/actionTypes';
import { AJAX_PUBLIC_REQUEST, AJAX_REQUEST, AJAX_REQUEST_WITH_FILE, GET_STORAGE, USER } from "../../Constants/AppConstants";
let user_info = '';

import ReactImageFallback from 'react-image-fallback';

class UserProfile extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showBottomNav: false,
      showHeaderNav: "hide",
      uploading: true,

      form: {
        profile_image: '',
        name: '',
        phone: '',
        email: '',
        address: ''
      },
    }
  }

  componentDidMount() {
    AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
      page_name: "profile",
      content_id: 0,
      module: "user",
    }).then(results => {            
    });
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    if(this.props.isAuthenticated){
      user_info = GET_STORAGE(USER);
      this.getUserInfo();
      this.getUserAddress();
    }
  }

  getUserInfo = () => {
    if (user_info) {
      AJAX_REQUEST('POST','user/getCustomerProfile',{
        customer_id: JSON.parse(user_info)['id']
      }).then(results => {
          if (parseInt(results.response.code) === 1000) {
            let oldForm = this.state.form;
            oldForm.profile_image = results.response.data.photo;
            oldForm.name = results.response.data.name;
            oldForm.phone = results.response.data.mobile_number;
            oldForm.email = results.response.data.email;
            this.setState({
              form: oldForm,
              uploading: false
            })
          }
        });
    }
  } 
  getUserAddress = () => {
    AJAX_PUBLIC_REQUEST('POST','user/getUserAddress',{
      customer_id: JSON.parse(user_info)['id']
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
          let oldForm = this.state.form;
          oldForm.address = (results.response.data.address ? results.response.data.address  : '') + (results.response.data.city ? ', '+results.response.data.city : '') + (results.response.data.zip_code ? ', '+results.response.data.zip_code : '');
          this.setState({
            form: oldForm
          })
        } else {
          
        }
      });
  }

  uploadProfileImage = (photoName) => {
    let { form } = this.state;
    form['profile_image'] = '../static/icon-loader.gif';
    // this.setState({ form: form });

    let data = new FormData();
    data.append('photo', photoName);

    AJAX_REQUEST_WITH_FILE("POST", "user/changePhoto", data).done(function (results) {
        if (parseInt(results.response.code) === 1000) {
            let oldForm = this.state.form;
            oldForm.profile_image = results.response.data;
            this.setState({
                uploading: false,
                form: oldForm,
            });
            console.log('updated', this.state.form)
        } else {
            let title = 'Error';
            let message = results.response.message;
            this.setState({ uploading: false })
        }
    }.bind(this));
}

  changeFile = (e) => {
    this.setState({ uploading: true })
    // this.uploadProfileImage(e.target.files[0]);
    if (event.target.files && event.target.files[0]) {
      let reader = new FileReader();
      reader.onload = (e) => {
        let oldForm = this.state.form;
        oldForm.profile_image = e.target.result;
        this.setState({
          form: oldForm
        });
      };
      reader.readAsDataURL(event.target.files[0]);
    }
}

photoChange = () => {
  // this.inputElement.click();
  if (typeof AndroidInterface != "undefined") {
    AndroidInterface.pickProfileImage(JSON.parse(user_info)['id']);
  } else {
    window.webkit.messageHandlers.pickProfileImage.postMessage(JSON.parse(user_info)['id']);
  }
  setInterval(function(){
    this.getUserInfo();
  }.bind(this), 1500);
}

onPageAfterIn = () => {
  //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
  // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
  setTimeout(function(){
    console.log("Action")
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);

    if(this.props.isAuthenticated){
      this.getUserInfo();
    }
    else {
      console.log(this);
      this.$f7.view[0].router.navigate('/');
    }
  }.bind(this), 10);
}

  render() {
    // if (this.props.isAuthenticated) {
    //   this.$f7router.navigate('/');
    // }
    return (
      <Frc.Page name="user-profile" className="user-profile-page" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <Frc.Navbar className="transparent user-profile-nav">
          <Frc.NavLeft>
            <Frc.Link iconIos="f7:chevron_left" iconMd="material:arrow_back" href="/"/>
            {/* <Frc.Link iconIos="f7:chevron_left" iconMd="material:arrow_back" panelOpen="left" text="Back"/> */}
          </Frc.NavLeft>
          <Frc.NavTitle></Frc.NavTitle>
          <Frc.NavRight>
            <Frc.Link className="edit-link" href="/edit-user/"><i className="far fa-edit"></i> Edit</Frc.Link>
          </Frc.NavRight>
        </Frc.Navbar>
        {/* Slider */}

        <Frc.Block className="user-profile-wrapper">
          <Frc.Block className="user-profile-container">
            <div className="user-profile-img">
              {/* <img src='../static/no-image.png' /> */}
                <ReactImageFallback
                  src={this.state.form ? this.state.form.profile_image : ''}
                  fallbackImage='./../static/placeholder_dark.svg'
                  initialImage='../static/icon-loader.gif'
                  alt='logo'
                  className="" />
              <input ref={input => this.inputElement = input} onChange={this.changeFile} name="photo" id="photo" type="file" required style={{visibility: 'hidden', opacity: 0, display: 'none'}} />
              <Frc.Link onClick={this.photoChange}><i className="fas fa-camera"></i></Frc.Link>
            </div>
            <div className="user-profile-info">
              <ul>
                <li>
                  <span><i className="fas fa-user"></i></span>
                  <p>
                    Name
                      <strong>{this.state.form ? this.state.form.name ? this.state.form.name : '---' : ''}</strong>
                  </p>
                </li>
                <li>
                  <span><i className="fas fa-phone-volume"></i></span>
                  <p>
                    Phone
                      <strong>{this.state.form ? this.state.form.phone : ''}</strong>
                  </p>
                </li>
                <li>
                  <span><i className="fas fa-home-lg-alt"></i></span>
                  <p>
                    Address
                      <strong>{this.state.form ? this.state.form.address ? this.state.form.address : '---' : ''}</strong>
                  </p>
                </li>
                <li>
                  <span><i className="fas fa-envelope"></i></span>
                  <p>
                    E-mail
                      <strong>{this.state.form ? this.state.form.email ? this.state.form.email : '---' : '--'}</strong>
                  </p>
                </li>
              </ul>
            </div>

          </Frc.Block>
        </Frc.Block>
        <div className="user-profile-cng-pws">
          <Frc.Link className="" href="/change-password/">Change Password <i className="fas fa-long-arrow-alt-right"></i></Frc.Link>
        </div>
      </Frc.Page>
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(UserProfile);