import React, { Fragment, Component } from 'react';
import Validator from 'srvalidator';
import history from '../../history';
import { connect } from 'react-redux';
import PropTypes from "prop-types";
import { AJAX_PUBLIC_REQUEST } from '../../Constants/AppConstants';
import {
    App,
    Panel,
    Views,
    View,
    Popup,
    Page,
    Navbar,
    Toolbar,
    NavRight,
    Link,
    Block,
    BlockTitle,
    LoginScreen,
    LoginScreenTitle,
    List,
    ListItem,
    ListInput,
    ListButton,
    BlockFooter,
    Button, Row, Col, NavLeft, Icon, Form
} from 'framework7-react';


import { userLoginRequest } from '../../Store/actions/loginActions';

class LoginForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            login_phone: '',
            login_password: '',
            loading: true,
            pleaseWait: false,
            alertStatus: false,
            showModal: false,
            successMessage: '',
            errorMessage: 'Please fix the following error.',

            validator: new Validator(),
            form: {
                login_phone: '',
                login_password: '',
                remember: true,
            },
            rules: {
                login_phone: 'required|int',
                login_password: 'required|min:3',
            },
            hasErrors: false,
            // errors: {},

        }
    }

    componentDidMount() {
        this.setState({ loading: false })
        AJAX_PUBLIC_REQUEST("POST", "setting/pageViewTracking",{
            page_name: "login",
            content_id: 0,
            module: "user",
        }).then(results => {            
        });
    }

    changeHandler = (e) => {
        let { form, rules } = this.state;
        form[e.target.name] = e.target.value;

        if (this.state.hasErrors) {
            this.state.validator.validate(form, rules);
        }

        this.setState({
            form: form,
            [e.target.name]: e.target.value,
        });
    }

    getError(name) {
        return this.state.validator.first(name);
    }

    onSubmit = (e) => {
        e.preventDefault();
        const { form, rules } = this.state;
        // this.state.validator.validate(form, rules);
        
        if(this.state.form.login_phone == '') {
            this.showToastIcon("Please Type a Phone number!", "Error");
            return;
        }
        else if(this.state.form.login_password == '') {
            this.showToastIcon("Please Type a Password!", "Error");
            return;
        }
        else if(this.state.form.login_password.length < 4) {
            this.showToastIcon("Please Type a Correct Password!", "Error");
            return;
        }

        this.setState({
            form: form,
            pleaseWait: true,
        });

        // if (this.state.validator.isValid()) {
            this.setState({ isLoading: true });
            this.props.userLoginRequest(this.state.form).then(results => {
                if (parseInt(results.response.code) == 1000) {

                    let title = 'Success';
                    let message = "Successfully Logged In";

                    this.showToastIcon(message, title);
                    
                    
                    if (typeof AndroidInterface != "undefined") {
                    AndroidInterface.updateCustomerId(results.response.data.customer_id, results.response.data.login_key);
                    } else {
                    var login_res_data = {"customer_id": results.response.data.customer_id, "login_key": results.response.data.login_key};
                    window.webkit.messageHandlers.updateCustomerId.postMessage(login_res_data);
                    }
                        
                    
                    this.$f7router.navigate('/');

                } else {
                    this.setState({ pleaseWait: false });
                    let title = 'Error';
                    let message = results.response.message;
                    this.showToastIcon(message, title)
                }
            });
        // } else {
        //     this.setState({
        //         pleaseWait: false,
        //         successMessage: "",
        //         errorMessage: "",
        //         hasErrors: true
        //     });
        //     return;
        // }
    }


    showToastIcon(text, type) {
        const self = this;
        let status_ios = (type == "Success") ? '<i class="f7-icons">checkmark_circle_fill</i>' : '<i class="f7-icons">info_circle_fill</i>';
        let status_andr = (type == "Success") ? '<i class="material-icons">check_circle</i>' : '<i class="material-icons">info</i>';
        // Create toast
        if (!self.toastIcon) {
          self.toastIcon = self.$f7.toast.create({
            icon: self.$theme.ios || self.$theme.aurora ? status_ios : status_andr,
            text: text,
            position: 'center',
            closeTimeout: 2000,
          });
          self.toastIcon.open();
        }
        else {
            self.toastIcon.close();
            self.toastIcon.destroy();
            self.toastIcon = self.$f7.toast.create({
                icon: self.$theme.ios || self.$theme.aurora ? status_ios : status_andr,
                text: text,
                position: 'center',
                closeTimeout: 2000,
            });
            self.toastIcon.open();
          }
      }

    render() {
        let { form } = this.state;

        return (
            // <Page className="loginPage" noSwipeback>
            <Fragment>
                {
                    (this.state.loading) ?
                        <div className="loading"></div>
                        :
                        <Fragment>
                            <div className="login-wrapper">
                                <div className="sign-in-logo">
                                    <img src='../static/speedonx-logo.svg' />
                                </div>
                                <h3 className="login-wrapper-title">Login</h3>
                                <div className="auth-sing-info">
                                    <List form>
                                        <ListInput
                                            type="number"
                                            placeholder="Mobile Number"
                                            value={form.login_phone}
                                            name="login_phone"
                                            onChange={this.changeHandler}
                                        ></ListInput>
                                        <ListInput
                                            type="password"
                                            placeholder="Your password"
                                            value={form.login_password}
                                            name="login_password"
                                            onChange={this.changeHandler}
                                        ></ListInput>
                                    
                                        <div className="auth-sing-btn-wrapper">
                                            <div className="forgot-link-pws">
                                                <Link href="/forget-pw/">Forgot Password?</Link>
                                            </div>
                                            <Button className="auth-sing-btn" type="submit" onClick={(e) => this.onSubmit(e)}>
                                                {this.state.pleaseWait ? <i class="fa fa-spin fas fa-spinner"></i> : <i className='far fa-arrow-right'></i>}
                                            </Button>
                                        </div>
                                    </List>
                                </div>

                            </div>
                            <div className="auth-sing-up-now">
                                <span>New to SpeedonX?</span>
                                <Link href="/sign-up/">	SignUp Now</Link>
                            </div>
                        </Fragment>
                }
                </Fragment>
            // </Page>
        );
    }
}

LoginForm.propTypes = {
    userLoginRequest: PropTypes.func.isRequired,
}

export default connect(null, { userLoginRequest })(LoginForm);