import React, { Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from './../../Store/actions/actionTypes';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, AJAX_REQUEST, GET_STORAGE, SET_STORAGE } from "./../../Constants/AppConstants";
import Modal from 'react-responsive-modal';
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import ReactImageFallback from 'react-image-fallback';

class ProductBook extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "hide",

      brand_id: '',
      product_id: '',
      model_id: '',
      engine_capacity: '',
      color: '',

      productData: [],
      photos: '',
      
      brands: [],
      models: [],
      engine_capacities: [],
      colors: [],

      productId: this.$f7route.params.productId,

      open: false,
      message: 'Something went wrong',
      status: 'error',

      openSignIn: true,
    }
  }

  handleSubmit = (event) => {
    event.preventDefault();

    if(this.state.openSignIn ==true) {
      this.showToastIcon();
      return;
    }
    else {
      if(this.state.model_id == '') {
        this.showToastWithButton("Please Select a Model!");
        return;
      }
      else if(this.state.engine_capacity == '') {
        this.showToastWithButton("Please Select an Engine Capacity!");
        return;
      }
      else if(this.state.color == '') {
        this.showToastWithButton("Please Choose a Color!");
        return;
      }
  
      let data = {
        brand_id: this.state.brand_id,
        product_id: this.state.product_id,
        model: this.state.model_id,
        engine_capacity: this.state.engine_capacity,
        color: this.state.color,
      };
  
      AJAX_REQUEST("POST", "product/saveBookNow", data).then(results => {
        if (parseInt(results.response.code) === 1000) {
              this.setState({
                open: true,
                status: results.response.status,
                message: results.response.message,

                brand_id: '',
                product_id: '',
                model_id: '',
                engine_capacity: '',
                color: '',
              })
        } else {
          this.showToastIcon(results.response.message)
        }
      });
    }
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    this.getBikeDetails();
    this.getBrands();
    this.$f7ready((f7) => {
      {
        this.props.isAuthenticated ?
        this.setState({
          openSignIn: false
        })
        :
        ""
      }
    });
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };
 
  onCloseModal = () => {
    this.setState({ open: false });
  };

  last_page = () => {
    var last_link = "/BikeDetails/product/"+this.state.productId+'/';
    return last_link;
  }

  getBikeDetails = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getProductDetails", {
      product_id: this.state.productId,
      category_id: 1,
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          loading: false,
          productData: results.response.data,
          photos: results.response.data.photo[0].image,
          brand_id: results.response.data['brand_id'],
          model_id: results.response.data['model_id'],
          product_id: results.response.data['id'],
          engine_capacity: results.response.data['engine_capacity'],

          models: [{
            "id": results.response.data['model_id'],
            "name": results.response.data['model_id']
          }],
          engine_capacities: [{
            "id": results.response.data['engine_capacity'],
            "name": results.response.data['engine_capacity']
          }],
          colors: [],
          
        });

        AJAX_PUBLIC_REQUEST("POST", "product/getModelList", {
          brand_id: results.response.data['brand_id'],
          category_id: 1,
        }).then(result => {
          if (parseInt(result.response.code) === 1000) {
            this.setState({
              models: result.response.data,
              model_id: results.response.data['model_id'],
              product_id: results.response.data['id'],
            });


            AJAX_PUBLIC_REQUEST("POST", "product/getEngineCapacityList", {
              brand_id: results.response.data['brand_id'],
              model_id: results.response.data['model_id'],
              category_id: 1,
            }).then(res => {
              if (parseInt(res.response.code) === 1000) {
                this.setState({
                  engine_capacity: results.response.data['engine_capacity'],
                  engine_capacities: res.response.data,
                  product_id: res.response.data[0]['product_id'],
                  photos: res.response.photo,
                });
              } else {
                this.setState({
                  errorMessage: res.response.message,
                })
              }
            });


          } else {
            this.setState({
              errorMessage: result.response.message,
            })
          }
        });


      } else {
        this.setState({
          loading: false,
          errorMessage: results.response.message,
        })
      }
    });
  }

  getBrands = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getBrandList", {
      // product_id: this.state.productId,
      category_id: 1,
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          brands: results.response.data,
          model_id: '',
          engine_capacity: '',
          color: '',
        });
      } else {
        this.setState({
          errorMessage: results.response.message,
        })
      }
    });
  }

  getModels = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getModelList", {
      brand_id: this.state.brand_id,
      category_id: 1,
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          models: results.response.data,
          model_id: '',
          engine_capacity: '',
          color: '',
          productData: [],
          photos: results.response.photo,
        });
      } else {
        this.setState({
          errorMessage: results.response.message,
        })
      }
    });
  }

  getEngineCapacityList = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getEngineCapacityList", {
      brand_id: this.state.brand_id,
      model_id: this.state.model_id,
      category_id: 1,
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          engine_capacities: results.response.data,
          engine_capacity: '',
          color: '',
          product_id: results.response.data[0]['product_id'],
          photos: results.response.photo,
        });
      } else {
        this.setState({
          errorMessage: results.response.message,
        })
      }
    });
  }

  getColorList = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getColorList", {
      brand_id: this.state.brand_id,
      model_id: this.state.model_id,
      engine_capacity: this.state.engine_capacity,
      category_id: 1,
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          colors: results.response.data,
        });
      } else {
        this.setState({
          errorMessage: results.response.message,
        })
      }
    });
  }

  showToastIcon() {
    const self = this;
    // Create toast
    if (!self.toastIcon) {
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<a href="/login/"><i class="f7-icons">info_circle_fill</i></a>' : '<a href="/login/"><i class="material-icons">info</i></a>',
        text: 'Please Log In First',
        position: 'center',
        closeTimeout: 1000,
      });
    }
    // Open it
    self.toastIcon.open();
    setTimeout(function(){
      self.$f7.view[0].router.navigate('/Login/');
    }, 1500);
  }

  showToastWithButton(text) {
    const self = this;
    // Create toast
    if (!self.toastWithButton) {
      self.toastWithButton = self.$f7.toast.create({
        text: text,
        closeButton: true,
        closeTimeout: 2000,
      });
      self.toastWithButton.open();
    }
    else {
      self.toastWithButton.close();
      self.toastWithButton.destroy();
      self.toastWithButton = self.$f7.toast.create({
        text: text,
        closeButton: true,
        closeTimeout: 2000,
      });
      self.toastWithButton.open();
    }
    
  }

  handleChange = event => {
    const { name, value } = event.target;
    
    if(name == 'brand_id') {
      this.setState({
        brand_id: value,
      },()=> {
        this.getModels();
      })
      
    }
    else if(name == 'model_id') {
      this.setState({
        model_id: value,
      },()=> {
        this.getEngineCapacityList();
      })
    }
    else if(name == 'engine_capacity') {
      this.setState({
        engine_capacity: value,
      },()=> {
        this.getColorList();
      })
    }
    else {
      this.setState({
          [name]: value
        },()=> {
          // console.log(name, value)
        })
    }
    
    // this.setState({
    //   [name]: value
    // },()=> {
    //   // console.log(name, value)
    // })
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
}


  render() {
    const { open } = this.state;
    let brandName = '';
    let brandId = this.state.brand_id;
    for(var i = 0; i < this.state.brands.length; i++)
    {
      if(this.state.brands[i].id == brandId)
      {
        brandName = this.state.brands[i].name;
      }
    }
    return (
      this.state.loading ?
      <Frc.Page onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <RandomLoader /> 
      </Frc.Page>
      :
      <Frc.Page name="user-profile" className="product-page" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        {/* Modal */}
        <Modal closeOnOverlayClick={false} modalId="signupPopup" closeIconId="closeSignupPop" open={open} onClose={this.onCloseModal} center>
          {
            this.state.status == "success" ?
            <Frc.Link className="successSignup" iconIos="f7:checkmark_alt_circle" iconMd="material:check_circle_outline" href="#" />
            :
            <Frc.Link className="errorSignup" iconIos="f7:info_circle_fill" iconMd="material:error" href="#" />
          }
          
          <h2>{this.state.message}</h2>
          <Frc.Link className="fw-login" href="/bikehome/" onClick={()=>this.onCloseModal()} animate={false} reloadCurrent={true}>Ok</Frc.Link>
        </Modal>

        <Frc.Navbar className="transparent product-page-book-nav">
          <Frc.NavLeft backLink>
              {/* <Frc.Link href={this.last_page()} iconIos="f7:chevron_left" iconMd="material:arrow_back" /> */}
          </Frc.NavLeft>
          <Frc.NavTitle></Frc.NavTitle>
        </Frc.Navbar>
        {/* Slider */}

        <Frc.Block className="no-padding-left	no-padding-right product-book-wrapper">
          <div className="product-book-img">
            {/* <img src='../static/bike/book/03.png' /> */}
            <ReactImageFallback
              src={this.state.photos}
              fallbackImage='./../static/placeholder_dark_big.svg'
              initialImage='./../static/placeholder_dark_big.svg'
              alt='image'
              className="swiper-lazy"
            />
          </div>
          <div className="product-book-info">
            <h3 className="product-book-info-title">BOOK YOUR DREAM BIKE</h3>
            <div className="product-book-brand">
              {/* <h3>{brandName} {this.state.model_id}</h3> */}
              <h3>{this.state.productData ? this.state.productData.title ? this.state.productData.title : brandName +' '+this.state.model_id : brandName +' '+this.state.model_id}</h3>
              <span>Engine Capacity : {this.state.productData ? this.state.productData.engine_capacity ? this.state.productData.engine_capacity : this.state.engine_capacity : '---'}</span>
            </div>
            <form onSubmit={this.handleSubmit}>
            <div className="product-book-info-form">
              <Frc.Input className="select-custom" type="select" name="brand_id" onChange={this.handleChange}>
              <option value="">Select Brand</option>
              {
                this.state.brands.length > 0 ?
                  this.state.brands.map((brand, index) =>
                    <option selected={brand['id']==this.state.brand_id ? 'selected' : ''} key={'brand'+index} value={brand['id']}>{brand['name']}</option>
                  )
              :
              ''
                  // <option value="">No Brands Found!</option>
              }
              </Frc.Input>
              <Frc.Input className="select-custom" type="select" name='model_id' onChange={this.handleChange}>
              <option value="">Select Model</option>
                {
                  this.state.models.length > 0 ?
                    this.state.models.map((model, index) =>
                      <option selected={model['id']==this.state.model_id ? 'selected' : ''} key={'model'+index} value={model['id']}>{model['name']}</option>
                    )
                :
                ''
                    // <option value="">No Models Found!</option>
                }
                {/* <option value={this.state.productData ? this.state.productData.id : '---'}>{this.state.productData ? this.state.productData.title : '---'}</option> */}
              </Frc.Input>
              <Frc.Input className="select-custom" type="select" name="engine_capacity" onChange={this.handleChange}>
              <option value="">Engine Capacity (CC)</option>
                {
                  this.state.engine_capacities.length > 0 ?
                    this.state.engine_capacities.map((engine_capacity, index) =>
                      <option selected={engine_capacity['id']==this.state.engine_capacity ? 'selected' : ''} key={'engine_capacity'+index} value={engine_capacity['id']}>{engine_capacity['name']}</option>
                    )
                :
                ''
                    // <option value="">No Engine Capacity Found!</option>
                }
                {/* <option value={this.state.productData ? this.state.productData.engine_capacity ? this.state.productData.engine_capacity : '---' : '---'}>{this.state.productData ? this.state.productData.engine_capacity ? this.state.productData.engine_capacity : '---' : '---'}</option> */}
              </Frc.Input>
              <Frc.Input className="select-custom" type="select" name="color" onChange={this.handleChange}>
                <option value="">Color</option>
                {/* {
                  this.state.colors.length > 0 ?
                    this.state.colors.map((color, index) =>
                      <option selected={color['id']==this.state.color ? 'selected' : ''} key={'color'+index} value={color['id']}>{color['name']}</option>
                    )
                :
                    <option value="">No Color Found!</option>
                } */}
                <option value="blue">Blue</option>
                <option value="gray">Gray</option>
                <option value="red">Red</option>
                <option value="silver">Silver</option>
              </Frc.Input>
              <Frc.Button className="btn-custom" type="submit">Book Now</Frc.Button>
            </div>
            </form>
          </div>

        </Frc.Block>

      </Frc.Page>
    );
  }
}

function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(ProductBook);