import React, { Fragment, Component } from 'react';
import Validator from 'srvalidator';
import history from '../../history';
import { connect } from 'react-redux';
import PropTypes from "prop-types";
import { AJAX_PUBLIC_REQUEST, GET_STORAGE, REMOVE_STORAGE } from '../../Constants/AppConstants';
import {
    App,
    Panel,
    Views,
    View,
    Popup,
    Page,
    Navbar,
    Toolbar,
    NavRight,
    Link,
    Block,
    BlockTitle,
    LoginScreen,
    LoginScreenTitle,
    List,
    ListItem,
    ListInput,
    ListButton,
    BlockFooter,
    Button, Row, Col, NavLeft, Icon, Form, Preloader
} from 'framework7-react';


import { userLoginRequest } from '../../Store/actions/loginActions';

class LoginFormSignUp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            form: {
                login_phone: JSON.parse(GET_STORAGE('SignUpInfo'))['mobile_number'],
                login_password: JSON.parse(GET_STORAGE('SignUpInfo'))['password'],
                remember: true,
            }
        }
    }

    componentDidMount() {
        this.openIndicator()
        this.onSubmit()
    }

    openIndicator = () => {
        const self = this;
        self.$f7.preloader.show('red');
        setTimeout(() => {
          self.$f7.preloader.hide();
        }, 1000);
    }

    onSubmit = () => {
        const { form } = this.state;

            this.props.userLoginRequest(form).then(results => {
                if (parseInt(results.response.code) == 1000) {

                    let title = 'Success';
                    let message = "Successfully Logged In";

                    this.showToastIcon(message, title);

                    REMOVE_STORAGE("SignUpInfo")
                    
                    
                    if (typeof AndroidInterface != "undefined") {
                    AndroidInterface.updateCustomerId(results.response.data.customer_id, results.response.data.login_key);
                    } else {
                    var login_res_data = {"customer_id": results.response.data.customer_id, "login_key": results.response.data.login_key};
                    window.webkit.messageHandlers.updateCustomerId.postMessage(login_res_data);
                    }                        
                    
                    this.$f7router.navigate('/');

                } else {
                    let title = 'Error';
                    let message = results.response.message;
                    this.showToastIcon(message, title)
                    this.$f7router.navigate('/');
                }
            });
    }


    showToastIcon(text, type) {
        const self = this;
        let status_ios = (type == "Success") ? '<i class="f7-icons">checkmark_circle_fill</i>' : '<i class="f7-icons">info_circle_fill</i>';
        let status_andr = (type == "Success") ? '<i class="material-icons">check_circle</i>' : '<i class="material-icons">info</i>';
        // Create toast
        if (!self.toastIcon) {
          self.toastIcon = self.$f7.toast.create({
            icon: self.$theme.ios || self.$theme.aurora ? status_ios : status_andr,
            text: text,
            position: 'center',
            closeTimeout: 2000,
          });
          self.toastIcon.open();
        }
        else {
            self.toastIcon.close();
            self.toastIcon.destroy();
            self.toastIcon = self.$f7.toast.create({
                icon: self.$theme.ios || self.$theme.aurora ? status_ios : status_andr,
                text: text,
                position: 'center',
                closeTimeout: 2000,
            });
            self.toastIcon.open();
          }
      }

    render() {
        return (
            <Page>
                
            </Page>
        );
    }
}

LoginFormSignUp.propTypes = {
    userLoginRequest: PropTypes.func.isRequired,
}

export default connect(null, { userLoginRequest })(LoginFormSignUp);