import React, { Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from './../../Store/actions/actionTypes';
import Modal from 'react-responsive-modal';
import { AJAX_REQUEST, AJAX_PUBLIC_REQUEST } from '../../Constants/AppConstants';

class ProductBook extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showBottomNav: true,
      showHeaderNav: "hide",

      currentStep: 1,

      brands: [],


      model: '',
      brand: '',
      oil_grade: '',
      oil_type: '',
      tentative_date: '',
      delivery_address: '',

      oil_types: [],

      openSignIn: true,

      open: false,
      message: 'Something went wrong',
      status: 'error',

    }
  }

  handleChange = event => {
    const { name, value } = event.target
    this.setState({
      [name]: value
    }, () => {
      // console.log(name, value)
    })
  }

  handleDateClick = (value) => {
    function pad(s) { return (s < 10) ? '0' + s : s; }
    var d = new Date(value);
    this.setState({
      tentative_date: [pad(d.getDate()), pad(d.getMonth() + 1), d.getFullYear()].join('/')
    })
    console.log(this.state.tentative_date)
  }

  handleSubmit = (event) => {
    event.preventDefault();

    if (this.state.tentative_date == '') {
      this.showToastWithButton("Please Select a Tentative Date!");
      return;
    }
    else if (this.state.delivery_address == '') {
      this.showToastWithButton("Please Give an Address!");
      return;
    }

    let data = {
      model: this.state.model,
      brand_id: this.state.brand,
      oil_grade: this.state.oil_grade,
      oil_type: this.state.oil_type,
      tentative_date: this.state.tentative_date,
      delivery_address: this.state.delivery_address,
    };

    AJAX_REQUEST("POST", "product/saveOilsubscription", data).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          open: true,
          status: results.response.status,
          message: results.response.message,

          currentStep: 1,
          model: '',
          brand: '',
          oil_grade: '',
          oil_type: '',
          tentative_date: '',
          delivery_address: '',
        })
      } else {
        // alert(results.response.message);
        this.showToastIcon(results.response.message)
      }
    });
  }

  _next = () => {
    if (this.state.openSignIn == true) {
      this.showToastIcon();
      return;
    }
    else {
      if (this.state.model == '') {
        this.showToastWithButton("Name of your bike can not be empty");
        return;
      }
      else if (this.state.brand == '') {
        this.showToastWithButton("Please Select a Brand");
        return;
      }
      let currentStep = this.state.currentStep
      currentStep = currentStep >= 2 ? 3 : currentStep + 1
      this.setState({
        currentStep: currentStep
      })
    }
  }

  _prev = () => {
    let currentStep = this.state.currentStep
    currentStep = currentStep <= 1 ? 1 : currentStep - 1
    this.setState({
      currentStep: currentStep
    })
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    this.getBrands();
    this.getOilTypes();
    this.$f7ready((f7) => {
      {
        this.props.isAuthenticated ?
          this.setState({
            openSignIn: false
          })
          :
          ""
      }
    });
  }

  getBrands = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getBrandList", {
      category_id: 2,
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          brands: results.response.data,
        });
      } else {
        this.setState({
          errorMessage: results.response.message,
        })
      }
    });
  }

  getOilTypes = () => {
    AJAX_PUBLIC_REQUEST("POST", "home/oilTypes", {
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          oil_types: results.response.data,
        });
      } else {
        this.setState({
          errorMessage: results.response.message,
        })
      }
    });
  }

  showToastIcon() {
    const self = this;
    // Create toast
    if (!self.toastIcon) {
      self.toastIcon = self.$f7.toast.create({
        icon: self.$theme.ios || self.$theme.aurora ? '<a href="/login/"><i class="f7-icons">info_circle_fill</i></a>' : '<a href="/login/"><i class="material-icons">info</i></a>',
        text: 'Please Log In First',
        position: 'center',
        closeTimeout: 1000,
      });
    }
    // Open it
    self.toastIcon.open();
    setTimeout(function () {
      self.$f7.view[0].router.navigate('/Login/');
    }, 1500);
  }

  showToastWithButton(text) {
    const self = this;
    // Create toast
    if (!self.toastWithButton) {
      self.toastWithButton = self.$f7.toast.create({
        text: text,
        closeButton: true,
        closeTimeout: 2000,
      });
      self.toastWithButton.open();
    }
    else {
      self.toastWithButton.close();
      self.toastWithButton.destroy();
      self.toastWithButton = self.$f7.toast.create({
        text: text,
        closeButton: true,
        closeTimeout: 2000,
      });
      self.toastWithButton.open();
    }

  }

  previousButton() {
    let currentStep = this.state.currentStep;
    if (currentStep !== 1) {
      return (
        <button
          className="insurance-btn-back"
          type="button" onClick={this._prev}>
          <Frc.Icon icon="icon-back"></Frc.Icon>
        </button>
      )
    }
    return null;
  }

  nextButton() {
    let currentStep = this.state.currentStep;
    if (currentStep < 2) {
      return (
        <button
          className="btn-custom button insurance-btn"
          type="button" onClick={this._next}>
          Next
        </button>
      )
    }
    return null;
  }

  last_page = () => {
    var last_link = "/oilhome/";
    return last_link;
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  closeSignIn = () => {
    this.setState({ openSignIn: false });
  };
  
  onPageAfterIn = () => {
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  render() {
    const { open } = this.state;
    return (
      <Frc.Page name="user-profile" className="insurance-page" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        {/* Modal */}
        <Modal modalId="signupPopup" closeIconId="closeSignupPop" open={open} onClose={this.onCloseModal} center>
          {
            this.state.status == "success" ?
              <Frc.Link className="successSignup" iconIos="f7:checkmark_alt_circle" iconMd="material:check_circle_outline" href="#" />
              :
              <Frc.Link className="errorSignup" iconIos="f7:info_circle_fill" iconMd="material:error" href="#" />
          }

          <h2>{this.state.message}</h2>
          <Frc.Link className="fw-login" href="#" onClick={() => this.onCloseModal()} animate={false} reloadCurrent={true}>Ok</Frc.Link>
        </Modal>

        <Frc.Navbar className="transparent insurance-page-nav">
          <Frc.NavLeft>
            {this.state.currentStep >= 2 ?
              this.previousButton()
              :
              <Frc.Link back iconIos="f7:chevron_left" iconMd="material:arrow_back" />
              // <Frc.Link href={this.last_page()} iconIos="f7:chevron_left" iconMd="material:arrow_back" />
            }
          </Frc.NavLeft>
          <Frc.NavTitle></Frc.NavTitle>
        </Frc.Navbar>
        {/* Slider */}

        <Frc.Block className="no-padding-left	no-padding-right product-book-wrapper">
          <form onSubmit={this.handleSubmit}>
            <Step1
              currentStep={this.state.currentStep}
              handleChange={this.handleChange}
              model={this.state.model}
              brand={this.state.brand}
              oil_grade={this.state.oil_grade}
              oil_type={this.state.oil_type}
              brands={this.state.brands}
              oil_types = {this.state.oil_types}
            />
            <Step2
              currentStep={this.state.currentStep}
              handleChange={this.handleChange}
              handleSubmit={this.handleSubmit}
              tentative_date={this.state.tentative_date}
              delivery_address={this.state.delivery_address}
              handleDateClick={this.handleDateClick}
            />
            {/* {this.previousButton()} */}
            {this.nextButton()}

          </form>
        </Frc.Block>

      </Frc.Page>
    );
  }

}

function Step1(props) {
  if (props.currentStep !== 1) {
    return null
  }
  return (
    <div className="insurance-wrapper">
      <div className="insurance-img">
        <img src='../static/oil-subscription.png' />
        <div className="subcribtion-form-title">
          <div className="insurance-form-title-text">
            SUBSCRIBE <br></br>ENGINE OIL
          </div>
        </div>
      </div>
      <div className="insurance-form subcribtion-form">

        <Frc.Input
          className="text-custom"
          type="text"
          placeholder="Bike Model Name with CC"
          name="model"
          onChange={props.handleChange}
          value={props.model}
        >
        </Frc.Input>

        <Frc.Input className="select-custom" type="select" onChange={props.handleChange} value={props.brand} name="brand">
          <option value="">Engine Oil Brand Name</option>
          {
            props.brands ?
              props.brands.length > 0 ?
                props.brands.map((brand, index) =>
                  <option key={'brand' + index} value={brand['id']}>{brand['name']}</option>
                )
                :
                ''
              :
              ''
          }
        </Frc.Input>

        <Frc.Input
          className="text-custom"
          type="text"
          placeholder="Engine Oil Grade (optional)"
          onChange={props.handleChange}
          name="oil_grade"
          value={props.oil_grade}
        >
        </Frc.Input>

        <Frc.Input className="select-custom" type="select" onChange={props.handleChange} value={props.oil_type} name="oil_type">
          <option value="">Engine Oil Type (optional)</option>
          {/* <option value="ATF">ATF</option> */}
          {
            props.oil_types ?
              props.oil_types.length > 0 ?
                props.oil_types.map((type, index) =>
                  <option key={'type' + index} value={type}>{type}</option>
                )
                :
                ''
              :
              ''
          }
        </Frc.Input>

      </div>
    </div>

  );
}

function Step2(props) {
  if (props.currentStep !== 2) {
    return null
  }
  return (
    <React.Fragment>
      <div className="insurance-wrapper">
        <div className="insurance-img">
          <img src='../static/oil-subscription.png' />
          <div className="subcribtion-form-title">
            <div className="insurance-form-title-text">
              SUBSCRIBE <br></br>ENGINE OIL
          </div>
          </div>
        </div>
        <div className="insurance-form">

          <Frc.Input
            id="datepickertentative"
            className="calender-custom"
            type="datepicker"
            placeholder="Next Date of Order/Purchase"
            calendarParams={{ closeOnSelect: 'true', inputReadOnly: true, header: true, headerPlaceholder: "Select Tentative Date", dateFormat: "dd/mm/yyyy", minDate: '' }}
            name="tentative_date"
            onCalendarChange={(value) =>
              props.handleDateClick(value)
            }
          />

          <Frc.Input
            className="textarea-custom"
            type="textarea"
            rows="4"
            cols="50"
            placeholder="Delivery Address"
            onChange={props.handleChange}
            value={props.delivery_address}
            name="delivery_address"
          >
          </Frc.Input>

          <Frc.Button type="submit" className="btn-custom">Submit</Frc.Button>
        </div>
      </div>
    </React.Fragment>
  );
}

function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(ProductBook);