import React, { Fragment, Component } from 'react';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { SITE_TITLE } from '../../Constants/AppConstants';
import { Page } from 'framework7-react';
import LoginForm from './LoginForm';

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showFooterNavbar: false,
            showHeaderNavbar: "onlybacklogin"
          }
        document.title = `Login - ${SITE_TITLE}`;
    }
    

    componentDidMount() {
        // console.log('ddd',this.$f7route);
        // this.$f7router.navigate('/');
        console.log(this.props.isAuthenticated)
        this.props.changeHeaderNavbarShow(this.state.showHeaderNavbar);
        this.props.changeFooterNavbarShow(this.state.showFooterNavbar);
    }

    onPageAfterIn = () => {
        setTimeout(function(){
          console.log("Action")
          this.props.changeFooterNavbarShow(this.state.showFooterNavbar);
          this.props.changeHeaderNavbarShow(this.state.showHeaderNavbar);
      
          if(this.props.isAuthenticated){
            this.$f7.view[0].router.navigate('/');
          }
        }.bind(this), 10);
      }

    render() {
        if (this.props.isAuthenticated) {
            this.$f7router.navigate('/');
            // this.f7route.route.load('/home/')
            // history.push('/#/home/');
            // location.reload(); 
        }
        return (
            <Page className="loginPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
                <LoginForm />
            </Page>
        );
    }
}

function mapStateToProps(state) {
    return {
      isAuthenticated: state.auth.isAuthenticated,
      setting: state.setting
    }
  }

const mapDispachToProps = (dispach) => {
    return {
        changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
        changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
    }
}

export default connect(mapStateToProps, mapDispachToProps)(Login);