import React, { Fragment, Component } from 'react';
import * as Frc from 'framework7-react';
import { connect } from 'react-redux';
import { CHANGE_FOOTER_NAVBAR_SHOW, CHANGE_HEADER_NAVBAR_SHOW } from '../../Store/actions/actionTypes';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, GET_STORAGE, SET_STORAGE, ADD_TO_CART, ITEM_COUNT } from "./../../Constants/AppConstants";
import { RandomLoader } from './../../pages/Common/ReactContentLoader';
import ReactImageFallback from 'react-image-fallback';
import SingleItem from './SingleItem';
import NoContent from '../Common/NoContent';

class Cart extends Component {

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: false,
      showHeaderNav: "back",

      // Cart Info
      products: [],
      subTotal: 0,
      discountTotal: 0,
      total: 0,

      checkoutUrl: '',



      // quantity: 1,
      // show: true,
      // max: 5,
      // min: 0
    }
  }

  componentDidMount() {
    this.setState({
      loading: false,
      checkoutUrl: "/Checkout/",
    })
    this.props.isAuthenticated ? 
    this.setState({
      checkoutUrl: "/CheckoutUser/",
    })
    : ''
    this.handel_cart_state();
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
  }

  //== calculation of cart summery ==//
  cal_cart_summery = () => {
    let subTotal = 0;
    let discountTotal = 0;
    let price = 0;
    if (ITEM_COUNT() > 0) {
      const cart = JSON.parse(GET_STORAGE("cart"));
      const redeem_point_status = false;
      // const redeem_point_status = GET_STORAGE("redeem_point_status") && (GET_STORAGE("redeem_point_status") == 'yes') ? true : false;
      cart.forEach(function (item, key) {
        price = parseInt(item.discounted_price) > 0 ? parseInt(item.discounted_price) : parseFloat(item.price);
        subTotal = subTotal + (parseInt(item.quantity) * price);
        item.discounted_price != '' ? discountTotal = discountTotal + (parseInt(item.quantity) * parseFloat(item.discounted_price)) : '';
      }.bind(this));

      this.setState({
        subTotal: subTotal,
        discountTotal: discountTotal,
        // total: discountTotal > 0 ? subTotal - discountTotal : subTotal
        total: subTotal
      });

    }
    else {
      this.setState({
        subTotal: 0,
        discountTotal: 0,
        total: 0
      });
    }
  }

  //== handle cart state ==//
  handel_cart_state = () => {
    // {
    //   this.props.isAuthenticated ?
        this.setState({
          products: JSON.parse(GET_STORAGE("cart")) ? JSON.parse(GET_STORAGE("cart")) : []
        })
    //     :
    //     ""
    // }
    this.cal_cart_summery();
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }


  

  render() {
    return (
      this.state.loading ?
        <Frc.Page className="p-b-45vw myCartPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
          <RandomLoader />
        </Frc.Page>
        :
        <Frc.Page name="" className="p-b-45vw myCartPage" onPageAfterIn={this.onPageAfterIn} noSwipeback>
          <Fragment>
            <div className="my-cart-list">
              {/* <ul className="ul-reset">
                {
                  this.state.products ?
                    this.state.products.length > 0 ?
                      this.state.products.map((product, index) => {
                        return (
                          <SingleItem
                            key={index}
                            handel_cart_state={this.handel_cart_state}
                            product={product} 
                            key={'cart' + product.id}
                          />

                        )
                      }
                      )
                      :
                      <li style={{ paddingLeft: 20 }}>
                        No Products on Cart!
              </li>
                    :
                    <li style={{ paddingLeft: 20 }}>
                      No Products on Cart!
            </li>
              }

              </ul> */}
              <table className="ul-reset" style={{borderCollapse: 'separate', borderSpacing: '0 7px', width: '100%'}}>
              <colgroup>
                  <col width="20%"></col>
                  <col width="60%"></col>
                  <col width="10%"></col>
                  <col width="10%"></col>
              </colgroup>
                <tbody>
                {
                  this.state.products ?
                      this.state.products.length > 0 ?
                        this.state.products.map((product, index) => {
                          return (
                            <SingleItem
                              key={index}
                              handel_cart_state={this.handel_cart_state}
                              product={product} 
                              key={'cart' + product.id}
                            />

                          )
                        }
                        )
                        :
                        <tr style={{ paddingLeft: 20, textAlign: 'center' }}>
                          <td>
                            <div className="notification-details" style={{width: '100%'}}>
                                <NoContent icon = 'fa fa-shopping-cart' text="No Products on Cart!" />
                            </div>
                          </td>
                        </tr>
                      :
                      // <tr style={{ paddingLeft: 20 }}>
                      //   No Products on Cart!
                      // </tr>
                      ''
                }
                </tbody>
              </table>
              <div className="cart-payable-ammount">
                <h3>Payable Amount</h3>
                <div className="cart-total-wrapper">
                  <div className="cart-total">
                    <span>Subtotal : ৳ <span className="price">{this.state.subTotal}</span></span>
                    {/* <span className="discount">Discount : ৳ <span className="price">{this.state.discountTotal}</span></span> */}
                  </div>
                  <div className="cart-total-price">
                    Total To Pay : ৳ <span className="price">{this.state.total}</span>
                  </div>
                </div>
              </div>


            </div>

            {
                  this.state.products ?
                    this.state.products.length > 0 ?
            <Frc.Link className="place-ord-btn" href={this.state.checkoutUrl}>Place Order</Frc.Link>
            :
            ''
            :
            ''
            }
          </Fragment>
        </Frc.Page >
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(Cart);