import React, { Component, Fragment } from 'react';
import * as Frc from 'framework7-react';

import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../Store/actions/actionTypes';
import { GET_STORAGE, USER, AJAX_REQUEST } from './../Constants/AppConstants';

import ReactImageFallback from 'react-image-fallback';
let user_info = '';

class LoyaltyCard extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showBottomNav: true,
      showHeaderNav: "show",

      user_info: [],
    }
  }
  componentDidMount() {

    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);

    if(this.props.isAuthenticated){
      user_info = GET_STORAGE(USER);
      this.getUserInfo();
    }  

  }

  getUserInfo = () => {
    if (user_info) {
      AJAX_REQUEST('POST','user/getCustomerProfile',{
        customer_id: JSON.parse(user_info)['id']
      }).then(results => {
          if (parseInt(results.response.code) === 1000) {
            this.setState({
              user_info: results.response.data
            })
          }
        });
    }
  } 

  onPageAfterIn = () => {

    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  render() {
    // const user = this.state.user;
    return (
      <Frc.Page className="" onPageAfterIn={this.onPageAfterIn} noSwipeback>
        <Fragment>
          <div className="loyalty-wrapper">
            <div className="loyalty-wrapper-card">
              <div className="loyalty-wrapper-card-img">
                {/* <img src='../static/loyalty/loyalty-card.png' /> */}
                <ReactImageFallback
                  src='../static/loyalty/loyalty-card.png'
                  fallbackImage='./../static/placeholder_dark_big.svg'
                  initialImage='./../static/placeholder_dark_big.svg'
                  alt='image'
                  className=""
                />
              </div>
              <div className="loyalty-wrapper-card-info">
                <div className="">
                  Card No.
                 <span> {this.state.user_info ? this.state.user_info['loyalty_card_no'] ? this.state.user_info['loyalty_card_no'] : '---' : '---'}</span>
                </div>
                <div className="">
                  NAME
                 <span> {this.state.user_info ? this.state.user_info['name'] : ''}</span>
                </div>
                <div className="">
                  Issue Date
                 <span> {this.state.user_info ? this.state.user_info['loyalty_issue_date'] ? this.state.user_info['loyalty_issue_date']: '---' : '---'}</span>
                </div>
                <div className="">
                  Expired Date
                 <span> {this.state.user_info ? this.state.user_info['loyalty_expired_date'] ? this.state.user_info['loyalty_expired_date'] : '---' : '---'}</span>
                </div>
              </div>
            </div>
          </div>
        </Fragment>
      </Frc.Page>
    );
  }
}

// export default BikeHome;

function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}

const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(LoyaltyCard);
