import React, { Component } from 'react';
import * as Frc from 'framework7-react';

import ReactImageFallback from 'react-image-fallback';
import { connect } from 'react-redux';
import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../Store/actions/actionTypes';
import { RandomLoader } from './../pages/Common/ReactContentLoader';
import { SITE_TITLE, AJAX_PUBLIC_REQUEST, GET_STORAGE, SET_STORAGE } from "./../Constants/AppConstants";
import NoContent from './Common/NoContent';

class Category extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      showBottomNav: true,
      showHeaderNav: "show",

      categories: [],

      errorMessage: '',
    }
  }

  componentDidMount() {
    this.props.changeFooterNavbarShow(this.state.showBottomNav);
    this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    this.getCategories();
  }

  getCategories = () => {
    AJAX_PUBLIC_REQUEST("POST", "product/getCategoryList", {
    }).then(results => {
      if (parseInt(results.response.code) === 1000) {
        this.setState({
          loading: false,
          categories: results.response.data,
        });
      } else {
        this.setState({
          loading: false,
          errorMessage: results.response.message,
        })
      }
    });
  }

  getCategoryOffer = () => {
    AJAX_PUBLIC_REQUEST("POST", "loyalty/getCategoryList", {}).then(results => {
        console.log(results.response.data);
        if (parseInt(results.response.code) === 1000) {
            this.setState({
                loading: false,
                categoryList: results.response.data,
            });
        } else {
          this.setState({
            loading: false,
            categoryList: [],
          });
        }
    });
  }

  onPageAfterIn = () => {
    //console.log(this.props.changeFooterNavbarShow(this.state.showBottomNav))
    // console.log(this.props.changeHeaderNavbarShow(this.state.showHeaderNav))
    setTimeout(function(){
      console.log("Action")
      this.props.changeFooterNavbarShow(this.state.showBottomNav);
      this.props.changeHeaderNavbarShow(this.state.showHeaderNav);
    }.bind(this), 10);
  }

  onPtrRefresh = (done) => {
    setTimeout(() => {
      this.getCategories();
      done();
    }, 1000);
  }

  render() {
    return (
      this.state.loading ?
        <Frc.Page name="category" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
          <RandomLoader />
        </Frc.Page>
        :
        <Frc.Page name="category" onPageAfterIn={this.onPageAfterIn} noSwipeback ptr onPtrRefresh={(e)=>this.onPtrRefresh(e)}>
          <Frc.Block className="theme-color mt-3vw" style={{marginTop: '0vw'}}>
            <div className="notification-wrapper">
            {/* <Frc.BlockTitle className="text-align-center page-inner-title">CATEGORIES</Frc.BlockTitle> */}
            <h3 className="inner-title">CATEGORIES</h3>

            <Frc.List accordionList className="categories-list-wrapper">

              {
                this.state.categories.length > 0
                ?
                this.state.categories.map((category, index) =>
                // <Frc.ListItem key={`cat`+index}>
                  <Frc.AccordionItem key={`cat`+index} style={{marginTop: '5px'}}>
                    <Frc.AccordionToggle className="item-link">
                      <div className="item-title">
                        <b>{category['name']}</b>
                      </div>
                      <div className="item-subtitle">
                        {category['tagline']}
                      </div>
                      <div className="categories-list-img">
                        {/* <img src='../static/categories/01.png' /> */}
                        <ReactImageFallback
                          src={category['photo']}
                          fallbackImage='./../static/placeholder_dark.svg'
                          initialImage='./../static/placeholder_dark.svg'
                          alt='image'
                          className=""
                        />
                      </div>
                    </Frc.AccordionToggle>
                    {
                    category['brand'].length > 0
                    ?
                    <Frc.AccordionContent>
                      <Frc.Block className="categories-list-inner-wrapper">
                        <Frc.List mediaList inset className="categories-list-inner">
                        {
                          category['brand'].map((brands, index) =>
                          category['id'] == 1 ?
                          <Frc.ListItem
                            key={`brand`+index}
                            link={"/list-bike/brand/"+brands['id']+"/"}
                            title={brands['name']}
                          >
                          <img slot="media" src={brands['photo']} width="44" />
                          </Frc.ListItem>
                          :
                          <Frc.ListItem
                            key={`brand`+index}
                            link={"/list-oil/brand/"+brands['id']+"/"}
                            title={brands['name']}
                          >
                            <img slot="media" src={brands['photo']} width="44" />
                          </Frc.ListItem>
                          )
                        }
                        </Frc.List>
                      </Frc.Block>
                    </Frc.AccordionContent>
                    :
                    ''
                    }
                  </Frc.AccordionItem>
                // </Frc.ListItem>
                )
              : 
              <div className="notification-details" style={{width: '100%'}}>
                  <NoContent icon = 'far fa-frown' text="No Category Found!" />
              </div>
            }
            </Frc.List>
            </div>
          </Frc.Block>

        </Frc.Page>
    );
  }
}


function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    setting: state.setting
  }
}


const mapDispachToProps = (dispach) => {
  return {
    changeHeaderNavbarShow: (val) => dispach({ type: CHANGE_HEADER_NAVBAR_SHOW, value: val }),
    changeFooterNavbarShow: (val) => dispach({ type: CHANGE_FOOTER_NAVBAR_SHOW, value: val }),
  }
}

export default connect(mapStateToProps, mapDispachToProps)(Category);